@include body-class(true) {

	&.wpmudev-forminator-forminator-settings .sui-box[data-nav="appearance-presets"] {
		&.sui-box-sticky {
			margin-bottom: 30px;
			.forminator-new-appearance-preset {
				margin-left: 10px;
			}
			.fui-bar-selectors {
				min-width: 220px;
			}
		}
	}

	#forminator-appearance-preset {
		.sui-box-header .sui-tag {
			margin-left: 10px;
		}
	}

	.forminator-for-desktop {
		@include media(max-width, md) {
			display: none !important;
		}
	}

	.forminator-for-mobile {
		@include media(min-width, md) {
			display: none !important;
		}
		.sui-form-field {
			min-width: 100%;
		}
		.sui-actions {
			display: flex;
			margin-left: -10px;
		}
		.sui-right-actions {
			display: flex;
			margin-left: auto;
		}
	}
}