@use "sass:math";

@include body-class(true) {

	#forminator-modal {

		#modal-share-feedback {
			max-width: 460px;

			.sui-box-header {

				.sui-description {
					margin-top: 10px;
					color: #666;
				}
			}

			.sui-box-body {

				.sui-box-settings-row {
					border-bottom: none;
					margin-bottom: 30px;
					padding-bottom: 0;

					&:last-child {
						margin-bottom: 0;
						padding-bottom: 0;
					}

					.sui-settings-label {
						font-size: 13px;
					}
				}

				.forminator-feedback-rating {

					.sui-side-tabs {
						width: 100%;

						.sui-form-field {
							margin: 0;

							.sui-tab-content {
								margin: 0;
							}
						}

						.sui-tabs-menu {
							flex-wrap: unset;

							.sui-tab-item {
								flex: auto;
								flex-basis: 100%;
								text-align: center;
							}
						}

						.forminator-feedback-rating-range {
							position: relative;
							height: 22px;
							margin-top: 5px;

							> span {
								position: absolute;
								font-size: 13px;
								line-height: 22px;
								color: #888;

								&.forminator-feedback-rating-range-min {
									left: 0;
								}

								&.forminator-feedback-rating-range-max {
									right: 0;
								}
							}
						}
					}
				}

				.forminator-feedback-additional-details {

					.sui-form-field {
						width: 100%;

						.sui-form-field {
							margin-bottom: 0;
						}

						.sui-description {
							color: #aaa;
							display: inline;
						}

						textarea {
							height: 80px;
						}

						.forminator-character-limit {
							display: block;
							text-align: right;
							font-size: 13px;
							color: #666;
							line-height: 24px;
							margin-top: 5px;
						}
					}

				}
			}


			.sui-box-footer {

				button {
					width: 100%;

					&.sui-icon-loader {

						&::before {
							color: #aaa;
							margin-right: 5px;
						}
					}
				}
			}
		}
	}
}
