@include body-class(true) {

	.sui-accordion {

		// DESIGN: Blocks (Flexbox)
		&-block {

			.sui-accordion-item {

				.sui-accordion-item-data {

					li {

						&[data-col="selector"] {

							span {
								margin-right: 0;
							}

							.fui-selector-label {
								display: flex;
								align-items: center;
								justify-content: center;
								color: palette(silver, medium);
								font: bold 12px/22px $font;
								letter-spacing: $font--letter-spacing;

								.fui-label-icon {
									width: 12px;
									height: 12px;
									flex: 0 0 auto;

									[class*="sui-icon-"] {
										display: block;

										&:before {
											display: block;
										}
									}

									+ .fui-label-text {
										margin-left: 5px;
									}
								}

								.fui-label-text {
									min-width: 1px;
									flex: 0 1 auto;
								}

								+ div.fui-selector-button {

									@include media(max-width, md) {
										margin-top: 10px;
									}

									@include media(min-width, md) {
										margin-left: 10px;
									}
								}

								@include media(min-width, md) {
									min-width: 1px;
									flex: 1;
								}
							}

							.fui-selector-button {

								@include media(min-width, md) {
									min-width: 120px;
									flex: 0 0 auto;
								}
							}

							.sui-select {
								min-width: unset;
								margin-left: 10px;
							}

							@include media(max-width, md) {
								min-width: 100%;
								display: block;
							}

							@include media(min-width, md) {
								display: flex;
								align-items: center;
								margin-left: auto !important;
							}
						}
					}

					&.sui-onload {

						li[data-col="selector"] {
							background-color: palette(silver, light);
							border-radius: $border-radius;

							.fui-selector-label,
							div.select-container {
								opacity: 0;
								pointer-events: none;
							}
						}
					}
				}

				.sui-accordion-item-title {
					@include media(max-width, md) {
						flex-wrap: wrap;
					}

					.sui-accordion-item-subtitle {
						font: 500 13px/22px 'Roboto';
						letter-spacing: -0.25px;
						color: $grey;
						margin-left: 20px;
						@include media(max-width, md) {
							margin-left: 0;
						}
					}
				}
			}
		}

		// DESIGN: Table
		// Must use in conjunction with .sui-table on the <table> element.
		&.sui-table {

			> tbody {

				.sui-accordion-item--open {

					+ .sui-accordion-item-content {

						&.sui-accordion-item--mobile {
							display: block;
							visibility: hidden;
							z-index: -1;
							
							@include media(max-width, md) {
								display: table-row;
								visibility: visible;
								z-index: 0;
							}
						}
					}
				}
			}
		}
	}
}