@use "sass:math";

@include body-class(true) {
	#modal-insert-fields {
		.sui-box-selectors {
			ul {
				li {
					label {
						height: 100%;
						input + span {
							height: 100%;
						}
					}
				}
			}
		}
	}

	.sui-tag.forminator-small-blue-tag {
		background-color: $tag--blue-background;
	}
  	&.sui-color-accessible .sui-tag.forminator-small-blue-tag {
		background-color: #000;
	}

	#create-pdf-modal {

		> .sui-box {
			max-width: 800px;
    		margin: 0 auto;
		}

		.sui-box-selectors {
			padding: 30px;

			ul {
				margin: 0 -15px;
				
				li {
					padding: 0 15px;

					label {
						background-color: #fff;
						box-shadow: initial;

						&.sui-disabled {
							background-color: #fff;

							> input ~ span {
								border-style: none;

								&.box-select-head {
									border-bottom: 1px solid rgba(230, 230, 230, 0.50) !important;
								}
							}
						}

						> span {

							&.pdf-template-img {
								padding: 10px;
								border: none;

								img {
									width: 100%;
								}
							}
						}
					}
				}
			}
		}
	}
}
