// ============================================================
// SUI WP Editor
// Import original shared-ui styles for WordPress Editor.
@use "sass:math";

@include body-class(true) {

	.sui-form-field {

		.sui-editor {

			z-index: 0;

			.wp-editor-wrap {
				box-sizing: border-box;

				// ELEMENT:
				// This element holds media buttons and editor tabs only.
				// .wp-editor-tools.hide-if-no-js {

				// 	// ELEMENT: Media buttons.
				// 	.wp-media-buttons {}

				// 	// ELEMENT: Editor tabs.
				// 	.wp-editor-tabs {}

				// 	// ELEMENT: Editor container.
				// 	+ .wp-editor-container {}
				// }

				// ELEMENT:
				// This element works as the main wrapper for all editor parts.
				.wp-editor-tools:not(.hide-if-no-js) {

					// ELEMENT: Editor tabs and media buttons.
					.wp-editor-tabs {
						width: 100%;
						display: flex;
						flex-wrap: wrap;

						.wp-switch-editor {
							height: auto;
							float: none;
							display: block;
							top: 0;
							margin: 0;
							padding: #{math.div($sui-gutter-md, 2) - 2px} $sui-gutter-md #{math.div($sui-gutter-md, 2) - 1px};
							border-width: 1px 1px 0 1px;
							border-style: solid;
							border-color: $white;
							border-radius: $border-radius $border-radius 0 0;
							background: $white;
							color: palette(gray, light);
							font: 500 12px/22px $font;
							letter-spacing: $font--letter-spacing;
							transition: $transition;

							&:hover,
							&:focus {
								color: palette(gray, dark);
							}
						}
					}

					// ELEMENT: Editor container.
					.wp-editor-container {
						background: $white;
						border: 1px solid palette(silver, soft);
						border-radius: $border-radius;
						box-shadow: none;

						div.mce-panel {
							border-width: 0 !important;
							border-radius: 0 0 $border-radius $border-radius;
							box-shadow: none;

							.mce-container-body {

								.mce-toolbar {

									.mce-btn-group {
										padding: 0;

										.mce-btn {
											margin: 0;
											border: 0;
											border-radius: 0;
											font-size: 16px;

											button {
												padding: 0;

												.mce-ico {
													width: 30px;
													height: 30px;
													color: palette(gray, default);
													font-size: 16px;
													line-height: 30px;
												}

												&:hover,
												&:focus {

													.mce-ico {
														color: $blue;
													}
												}
											}

											&:hover,
											&:focus {
												background: transparent;
												box-shadow: none;
											}

											&.mce-active {
												background: transparent;
												box-shadow: none;

												button {
													background-color: palette(blue, light);

													.mce-ico {
														color: $blue;
													}
												}
											}
										}
									}
								}

								.mce-top-part {

									&:before {
										content: unset;
										display: none;
									}

									.mce-container-body {

										div.mce-toolbar-grp {
											border-bottom-width: 1px !important;
											border-bottom-color: palette(silver, soft);
											border-radius: 0 $border-radius 0 0;
											background: $white;
										}

										div.mce-toolbar-grp > div {
											padding: 4px;
										}
									}
								}
							}
						}

						.quicktags-toolbar {
							padding: 4px;
							border-bottom-color: palette(silver, soft);
							border-radius: $border-radius $border-radius 0 0;
							background: $white;

							input.button,
							input.button.button-small {
								min-width: 30px;
								min-height: 30px;
								margin: 0;
								border: 0;
								padding: 0 2px;
								background: $white;
								color: palette(gray, default);
								font-size: 13px;
								line-height: 30px;

								&:hover,
								&:focus {
									background-color: $white;
									outline: none;
									box-shadow: none;
									color: $blue;
								}
							}
						}

						textarea {
							width: 100%;
							display: block;
							margin: 0;
							padding: 10px;
							border-width: 0;
							border-radius: 0 0 $border-radius $border-radius;
							background: $white;
							height: 200px;

							&:hover,
							&:focus {
								outline: none;
								box-shadow: none;
							}
						}
					}
				}

				// VARIATION:
				// Tab for "visual" editor is selected.
				&.tmce-active {

					.wp-editor-tools:not(.hide-if-no-js) {

						.wp-editor-tabs {

							.wp-switch-editor {

								&.switch-tmce {
									color: palette(gray, dark);
									border-color: palette(silver, soft);
								}
							}
						}

						.wp-editor-container {
							border-top-left-radius: 0;
						}
					}
				}

				// VARIATION:
				// Tab for "text" editor is selected.
				&.html-active {

					.wp-editor-tools:not(.hide-if-no-js) {

						.wp-editor-tabs {

							.wp-switch-editor {

								&.switch-html {
									color: palette(gray, dark);
									border-color: palette(silver, soft);
								}
							}
						}
					}
				}
			}

			.sui-editor-options {
				pointer-events: none;

				* {
					pointer-events: all;
				}

				button {
					cursor: pointer;
					display: block;
					margin: 0;
					padding: 0;
					border: 0;
					border-radius: 0;
					background: $white;
					background-image: none;
					color: $blue;
					font: 700 12px/22px $font;
					margin-left: auto;

					[class*="sui-icon-"] {
						width: 20px;
						height: 30px;
						display: inline-flex;
						align-items: center;
						justify-content: center;

						&:before {
							display: block;
							color: inherit;
						}
					}
				}

				ul {
					max-height: 120px;
					overflow-x: hidden;
					overflow-y: auto;
					display: none;
					margin: #{math.div($sui-gutter-md, 2)} 0 0 -#{math.div($sui-gutter-md, 2)};
					padding: 5px 0;
					border: 1px solid palette(silver, soft);
					border-radius: $border-radius;
					background-color: $white;
					list-style: none;
					text-align: left;

					&:before {
						content: " ";
						width: 0;
						position: absolute;
						z-index: 1;
						top: #{$sui-gutter};
						right: #{math.div($sui-gutter, 4)};
						border-width: 0 8px 10px;
						border-style: solid;
						border-color: palette(gray, lighter) transparent;
					}

					&:after {
						content: " ";
						width: 0;
						position: absolute;
						z-index: 2;
						top: #{$sui-gutter + 1px};
						right: #{math.div($sui-gutter, 4)};
						border-width: 0 8px 10px;
						border-style: solid;
						border-color: $white transparent;
					}

					li {
						margin: 0;
						padding: 0;
						border: 0;
						color: palette(gray, dark);
						font: 400 13px/22px $font;
						letter-spacing: $font--letter-spacing;
						list-style: none;

						strong {
							display: block;
							padding: 5px 14px;
						}

						a, button {
							cursor: pointer;
							display: block;
							padding: 5px 14px;
							background-color: $white;
							color: palette(gray, default);
							font: 500 13px/22px $font;
							letter-spacing: $font--letter-spacing;
							transition: $transition;

							&:hover,
							&:focus {
								background: palette(blue, light);
								outline: none;
								box-shadow: none;
							}
						}
					}

					&.sui-active {
						display: block;
					}
				}
			}

			// VARIATION:
			// Editor with dropdown options.
			&.sui-editor-with-options {
				position: relative;

				.wp-editor-wrap {

					.wp-editor-tools:not(.hide-if-no-js) {

						.wp-editor-container {

							div.mce-panel .mce-container-body .mce-top-part {

								.mce-container-body div.mce-toolbar-grp > div {
									padding-left: 4px;
								}
							}
						}
					}
				}

				.sui-editor-options {
					display: block;
					position: absolute;
					z-index: 2;
					top: 45px;
					right: 15px;
				}
			}
		}

		// STATE: Error.
		&.sui-form-field-error {

			.sui-editor {

				.wp-editor-tools:not(.hide-if-no-js) {

					.wp-editor-container {
						border-bottom-color: $red;
						box-shadow: 0 1px 0 0 $red;
					}
				}
			}
		}
	}
}