@include body-class(true) {
	.forminator-rating-preview {
		padding: 20px;
		justify-content: center;
		background: #f9f9f9;
		border-radius: 4px;
	}

	.forminator-rating-field {
		display: flex;
		font-size: 24px;
		align-items: center;
		gap: 10px;
		
		.forminator-rating-items {
			display: flex;
			gap: 10px;
			flex-wrap: wrap;
			justify-content: center;
		}

		&.forminator-rating-size--sm {
			font-size: 16px;
		}

		&.forminator-rating-size--lg {
			font-size: 32px;
		}

		.forminator-rating-active {
			&:before {
				color: palette(orange, default);
			}
		}
	}

	.forminator-rating-suffix {
		font: 13px/22px $font;
		letter-spacing: -0.25px;
		color: #888;
	}
	
}