@include body-class(true) {

	#forminator-modal-body--address {

		> .sui-tabs {

			> .sui-tabs-content {

				> .sui-tab-content {

					> .sui-accordion-flushed {
						margin-top: -#{$sui-gutter-md};
						margin-bottom: -#{$sui-gutter-md};

						@include media(min-width, md) {
							margin-top: -#{$sui-gutter};
							margin-bottom: -#{$sui-gutter};
						}
					}
				}
			}
		}
	}

	#forminator-modal.preview .forminator-geolocation-map-wrapper {
		overflow-x: auto;
	}
}