@use "sass:math";

@include body-class(true) {
	.forminator-create-popup-sidebar {
		background: palette(silver, light);
		border-bottom-left-radius: $border-radius;
		border-bottom-right-radius: $border-radius;

		.sui-row-with-sidenav {
			margin-bottom: 0;
		}

		.forminator-search-result {
			font-size: 13px;
			line-height: 20px;
			margin-bottom: 30px;

			strong {
				color: $gray-alt;
			}
		}

		.forminator-template-message-box {
			box-shadow: 0 2px 0 $box-box-shadow-color;
			margin-bottom: 30px;
		}

		.forminator-template-image {
			height: 150px;
			margin: 10px;

			.forminator-template-image-wrapper {
				height: 100%;
				overflow: hidden;
			}

			.forminator-template-icon {
				display: flex;
				height: 100%;
				align-items: center;
				justify-content: center;
				border: 1px solid palette(silver, soft);
				border-radius: 4px;
			}
		}

		.forminator-preset-template {
			box-shadow: unset;
			&.sui-hidden {
				display: none !important;
			}
			.sui-box-selector {
				&.sui-disabled {
					background: $white;

					input {
						& ~ span {
							border-left: 0;
							border-right: 0;
						}
					}
				}

				.forminator-template-name {
					display: block;
					border-top: 1px solid rgba(230, 230, 230, 0.50);
					padding: 12px 20px;
					font-size: 15px;
					line-height: 30px;
					font-weight: 500;
					background: $white;
					transition: ease all 0.3s;
					color: #666666;
					text-align: left;
				}

				&:hover {
					.forminator-template-name {
						transform: translateY(-35px);
					}

					.forminator-template-image .forminator-preview-cta,
					.forminator-card-cta {
						opacity: 1;
					}

					.forminator-card-cta {
						transform: translateY(-45px);
					}

					.forminator-template-icon svg path {
						fill: #17A8E3;
					}

					&.forminator-dashboard-blank-template {
						border: 1px solid #17A8E3;
					}
				}

				.forminator-card-cta {
					display: flex;
					position: absolute;
					left: 0;
					right: 0;
					opacity: 0;
					overflow: hidden;
					padding: 0 20px 12px;
					transition: ease all 0.3s;

					.sui-button {
						width: 100%;
					}
				}
			}

			.forminator-template-image {
				position: relative;

				.forminator-preview-cta {
					position: absolute;
					inset: -10px;
					background: rgba(51, 51, 51, 0.95);
					opacity: 0;
					transition: ease all 0.1s;

					.sui-button {
						position: absolute;
						top: 40%;
						left: 50%;
						transform: translate(-50%, -50%);
					}
				}
			}
		}

		.forminator-saved-template {
			&.sui-hidden {
				display: none !important;
			}

			.sui-box-selectors {
				min-height: 670px;

				@media (max-width: 1199px) {
					min-height: 100%;
				}

				ul {
					li {
						padding: 5px 10px;
					}
				}
			}

			.sui-box-selector {
				display: flex;
				gap: 5px;
				padding: 15px 30px 15px 15px;

				@include media(max-width, md) {
					flex-wrap: wrap;
					padding: 15px;
				}

				.forminator-template-name {
					font-size: 13px;
					font-weight: 500;
					flex: 1;
					color: #333;
				}

				.forminator-icon {
					align-self: center;
				}

				.sui-button {
					margin-left: auto;
					opacity: 0;
					pointer-events: none;
					transition: ease all 0.3s;

					@include media(max-width, md) {
						opacity: 1;
						pointer-events: all;
						flex-basis: 100%;
						margin-top: 5px;
					}
				}

				&:hover {
					.sui-button {
						opacity: 1;
						pointer-events: all;
					}
				}
			}
		}

		.sui-sidenav {
			border-right: 1px solid palette(silver, soft);
			padding: 30px 15px;
			width: 250px;
			background: $white;
			border-bottom-left-radius: $border-radius;

			@include media(max-width, lg) {
				width: 100%;
				border: 0;
				margin-bottom: 0;
			}

			.sui-vertical-tabs {
				.sui-vertical-tab {
					.forminator-tag-number {
						background: transparent;
						font-size: 15px;
						line-height: 20px;
						top: 5px;
						color: palette(gray, default);
						font-weight: 500;
					}

					.forminator-tag-pro {
						top: 6px;
					}
				}
			}

			.forminator-category {
				border-top: 1px solid palette(silver, soft);
				padding-top: 30px;

				.sui-vertical-tabs {
					margin-bottom: 0;
				}

				.forminator-category-name {
					padding: 5px 15px;

					@include media(max-width, lg) {
						padding: 5px 0;
					}
				}
			}
		}

		.sui-sidenav-content {
			padding: 20px;
			max-height: 710px;
			overflow: auto;

			@include media(max-width, lg) {
				max-height: 470px;
			}

			@include media(max-width, md) {
				max-height: 350px;
			}

			.forminator-loading-state {
				justify-content: center;
				font-size: 13px;
				line-height: 20px;
				color: palette(gray, light);
			}

			.forminator-preset-template  {
				.sui-box-selectors {
					&.sui-box-selectors-col-3 {
						ul {
							li {
								@include media(between, sm, md) {
									min-width: 50%;
									flex-basis: 50%;
								}
							}
						}
					}
				}
			}
		}

		.forminator-loading-state {
			display: flex;
			padding: 10px 15px;
			align-items: center;
			gap: 10px;
			font-weight: 500;
			line-height: normal;

			.sui-loading {
				line-height: normal;
			}
		}
	}

	.forminator-preview-image {

		&.sui-box-body {
			padding: 0;
			line-height: 0;
			max-height: 700px;
			overflow-y: scroll;
		}

		img {
			width: 100%;
		}
	}
}
