@use "sass:math";

@include body-class(true) {

	#fui-stripe-plans {

		.sui-accordion-item {
			box-shadow: 0 2px 7px 0 rgba(0,0,0,0.05);

			.sui-accordion-item-header {
				min-height: auto;

				.sui-accordion-item-title {
					font-size: 13px;
					line-height: 22px;
				}

				.sui-accordion-col-auto {

					.fui-has_conditions {
						width: 20px;
						height: 20px;
						margin-right: -#{($sui-gutter - 1px) + 10px};
						border-radius: 50%;
						background-color: $white;
						box-shadow: 0 2px 6px 0 rgba(0,0,0,0.06);
						text-align: center;

						[class*="sui-icon-"] {
							width: 20px;
							height: 20px;
							display: flex;
							align-items: center;
							justify-content: center;
							color: palette(yellow);

							&:before {
								display: block;
								color: inherit;
							}
						}
					}
				}
			}

			.sui-accordion-item-body {
				padding-right: 0;
				padding-bottom: 0;
				padding-left: 0;
				border-top: 1px solid #E6E6E6;

				.sui-tabs:not(.sui-side-tabs) {

					&.sui-tabs-flushed {

						> .sui-tabs-menu {
							padding: 0 $sui-gutter-md;

							@include media(min-width, md) {
								padding: 0 $sui-gutter;
							}
						}

						> .sui-tabs-content {

							> .sui-tab-content {
								padding: $sui-gutter-md;

								@include media(min-width, md) {
									padding: $sui-gutter;
								}
							}
						}
					}
				}

				.sui-side-tabs {

					&.fui-plan-amount {

						.sui-tabs-content {

							.sui-tab-content {

								.fui-fixed-plan {
									display: flex;
									flex-flow: row wrap;
									align-items: center;

									.sui-form-control {
										width: auto;
										flex: 1 1 auto;
									}

									.sui-field-suffix {
										flex: 0 0 auto;
									}

									.sui-error-message {
										flex: 0 0 100%;
									}
								}
							}
						}
					}
				}
			}

			&:not(:last-child) {
				margin: 0 0 #{math.div($sui-gutter, 2)};
			}

			// STATE: Has Error.
			&.validation-error {
				box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.05), 0 2px 0 0 $red;
			}
		}
	}
}