@include body-class(true) {

	.fui-buttons-alignment {
		display: flex;
		align-items: center;
		justify-content: space-between;

		.sui-button,
		.sui-button-icon {
			flex: 0 1 auto;
		}

		&.fui-alignment-center {
			justify-content: center;
		}

		&.fui-alignment-right {
			justify-content: flex-end;
		}
	}

	.mtop10 {
		margin-top: 10px;
	}

	.mbottom10 {
		margin-bottom: 10px;
	}

	.fui-trend {
		font-size: 13px;
		line-height: 22px;
		font-weight: 700;
		margin-left: 5px;

		&.fui-trend-green {
			color: $success;
		}

		&.fui-trend-red {
			color: $error;
		}

		[class*="sui-icon-"] {
			margin-right: 5px;

			&:before {
				color: inherit;
			}
		}
	}

	.sui-colorpicker-wrap {

		.wp-picker-container {

			.wp-picker-holder {
				position: static;
				box-shadow: none;

				.iris-palette {
					box-sizing: border-box;
				}
			}
		}
	}
}