@use "sass:math";

@include body-class() {

	#fui-table-stripe {

		thead tr th {

			&:nth-child(2n+1) {

				@include media(max-width, md) {
					width: 0;
					overflow: hidden;
					visibility: hidden;
					white-space: nowrap;
				}
			}

			&:nth-child(2n+2) {

				@include media(max-width, md) {
					padding-top: #{$sui-gutter-md - 1};
					padding-bottom: #{$sui-gutter-md - 1};
				}
			}

			&:nth-child(5n+2) {

				@include media(max-width, md) {
					border-left: 1px solid #e6e6e6;
				}
			}

			&:nth-child(5n+4) {

				@include media(max-width, md) {
					border-right: 1px solid #e6e6e6;
				}
			}

			@include media(max-width, md) {
				display: table-cell;
			}
		}
	}

	#fui-table-stripe {

		tbody tr.sui-accordion-item {

			td {

				&:nth-child(2n+1) {

					@include media(max-width, md) {
						width: 0;
						overflow: hidden;
						visibility: hidden;
						white-space: nowrap;
					}
				}

				&:nth-child(5n+2) {

					@include media(max-width, md) {
						border-left: 1px solid #e6e6e6;
					}
				}

				&:nth-child(5n+4) {

					@include media(max-width, md) {
						border-right: 1px solid #e6e6e6;
					}
				}

				&:nth-last-child(2) {
					position: relative;

					@include icon(before, "chevron-down") {
						position: absolute;
						top: 50%;
						right: 10px;
						transform: translateY(-50%);

						@include media(min-width, md) {
							content: " ";
							display: none;
						}
					}
				}

				@include media(max-width, md) {
					display: table-cell;
				}
			}

			&.sui-accordion-item--open {

				td {

					&:nth-last-child(2) {

						&:before {
							transform: translateY(-50%) rotate(180deg);
						}
					}
				}
			}
		}

		.fui-stripe-row {
			display: block;

			.fui-col {
				display: block;
				margin: 0 0 $sui-gutter;

				// Second column of 5.
				&:nth-child(5n+2) {

					@include media(min-width, md) {
						width: 42.8571%;
					}
				}

				// Ninth column of 10.
				&:nth-child(10n+9) {

					&:last-child {

						@include media(min-width, md) {
							width: 28.5714%;
						}
					}
				}

				// Label.
				.col-label {
					margin: 0;
					color: #333;
					font-size: 13px;
					font-weight: 500;
					line-height: 22px;

					+ .col-value {
						margin: 5px 0 0;

						@include media(min-width, md) {
							margin: 10px 0 0;
						}
					}
				}

				.col-value {
					margin: 0;
					color: #888;
					font-size: 13px;
					line-height: 22px;
				}

				&:last-child {
					margin: 0;
				}

				@include media(min-width, md) {
					width: 14.2857%; // Based on 7-Grid.
					flex: 0 0 auto;
					margin: 0;
					padding: #{math.div(($sui-gutter + 10), 2)} #{math.div($sui-gutter, 2)};
				}
			}

			@include media(min-width, md) {
				display: flex;
				flex-flow: row wrap;
				margin: -#{math.div(($sui-gutter + 10), 2)} -#{math.div($sui-gutter, 2)};
			}
		}
	}

	#fui-table-group {

		thead tr th {
			padding-top: 5px;
			padding-bottom: 5px;
			@include media(max-width, md) { 
				padding-top: 20px;
				padding-bottom: 20px;
			}
		}

		tbody tr.sui-accordion-item td {

			@include media(max-width, md) { 
				padding-top: 20px !important;
				padding-bottom: 20px !important;
			}
		}
	}
}