@include body-class(true) {

	ul.sui-list-pro {
		margin: 0 0 30px;

		&, li {
			padding: 0;
			border: 0;
		}

		li {
			margin: 0 0 20px;
			color: palette(gray, dark);
			font: 500 15px/22px $font;
			letter-spacing: $font--letter-spacing;

			[class*="sui-icon-"] {
				width: 20px;
				height: 20px;
				vertical-align: top;
				margin-right: 10px;

				&:before {
					display: block;
					color: palette(purple);
					text-align: center;
				}
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}