@include body-class(true) {

	#forminator-form-instant-preview {

 		>.sui-box-header {

			.sui-tooltip {
				margin-left: 10px;
				cursor: help;
			}
		}
	}

	#forminator-instant-preview {
		.forminator-instant-preview-separator {
			display: flex;
			align-items: center;
			text-align: center;
			background-color: #fff;
			padding: 20px 0;
			&:before,
			&:after {
				content: '';
				flex: 1;
				border-bottom: 2px dashed #888;
			}
			&:before {
				margin-right: 10px;
			}
			&:after {
				margin-left: 10px;
			}
		}

		form.forminator-ui {
			max-width: 1000px;
			margin: auto;

			.forminator-field.forminator-custom-input {
				display: block;
			}
		}
	}
}