@use "sass:math";

@include body-class() {

	.forminator-category-name {
		font-size: 13px;
		font-weight: 700;
		line-height: 20px;
		text-transform: uppercase;
		color: palette(silver, medium);
	}

	.sui-tag.sui-tag-free-plan {
		background: #0059FF;
		color: $white;
		padding: 3px 10px;
		line-height: normal;
		min-height: unset;
	}

	.sui-wrap a.forminator-link-bright-blue {
		color:#0059FF;
		&:hover,
		&:focus {
			color: darken( #0059FF, 18% );
		}
	}

	.forminator-card {
		height: 100%;

		.sui-tag {
			position: absolute;
			top: 14px;
			right: 12px;
		}

		&.forminator-blank-card {
			cursor: pointer;
		}
	}

	.sui-wrap {
		.sui-button {
			&.sui-button-bright-blue {
				background-color: #0059FF;
				&:hover,
				&:focus {
					background-color: darken( #0059FF, 18% );
				}
			}
		}
		.sui-tag-free-plan {
			background: #0059FF;
			color: $white;
			padding: 3px 10px;
			line-height: normal;
			min-height: unset;
		}
	}

	.forminator-card-image,
	.forminator-template-image {
		img {
			object-fit: cover;
			object-position: top;
			border-radius: 4px;
			height: 100%;
			width: 100%;
		}
	}

	#forminator-templates {

        .sui-tabs-menu {
            border-top: 0;
            padding: 0 $sui-gutter-md;
            border-radius: $border-radius;
            background-color: $white;
            box-shadow: 0 2px 0 0 $box-box-shadow-color;
			display: flex;
			gap: 10px;

            .sui-tab-item {
				margin-left: 0;
				margin-right: 0;
                padding-top: 24px;
                padding-bottom: 22px;
                font-size: 12px;

                .sui-tag {
                    margin-left: 4px;
                }
            }

            .sui-tab-search {
                cursor: default;
                margin-right: 0;
                margin-left: auto;
            }

            @include media(min-width, lg) {
                padding: 0 $sui-gutter;
            }
        }

		.sui-progress-block {
			background: palette(silver, default);
			padding: 15px 20px;
			border: none;

			.sui-progress-text {
				font-size: 13px;
				font-weight: 400;
				line-height: 22px;
				letter-spacing: -0.25px;
				color: $gray-alt;
			}
		}

		.sui-box-builder {
			&.sui-flushed {
				border-radius: $border-radius;
			}
			.sui-builder-fields {
				.sui-builder-field {
					gap: 10px;
					height: auto;

					@include media(max-width, md) {
						flex-wrap: wrap;
					}

					&:last-child {
						margin-bottom: 0;
					}

					.sui-field-actions {
						.sui-button {
							opacity: 0;
							pointer-events: none;

							@include media(max-width, md) {
								opacity: 1;
								pointer-events: all;
								flex: 1;
							}
						}

						@include media(max-width, md) {
							display: flex;
							flex-basis: 100%;
						}
					}

					.sui-builder-field-label:first-child>[class*=sui-icon-] {
						@include media(max-width, md) {
							margin-left: -5px;
						}
					}

					&:hover {
						.sui-field-actions {
							.sui-button {
								opacity: 1;
								pointer-events: all;
							}
						}
					}
				}
			}
		}

		// ELEMENT: Box Message
		.sui-message {
			.sui-message-content {
				max-width: 860px;
			}
		}

		.forminator-search-field {
			flex: 1;

			@include media(max-width, md) {
				margin-bottom: 20px;

				.sui-input-md {
					max-width: 100%;
				}
			}
		}

		.sui-box-footer {
			.forminator-page-results {
				@include media(max-width, sm) {
					margin-bottom: 10px;
				}
			}
		}

		.forminator-category-name {
			padding: 0 0 0 15px;
			margin-bottom: 10px;

			@include media(max-width, lg) {
				padding: 0;
			}
		}

		.sui-sidenav .sui-vertical-tabs .sui-vertical-tab .sui-tag {
			top: 4px;
			background: transparent;
		}

		.sui-vertical-tabs {
			margin-top: 10px;
		}

		.forminator-loading-state,
		.sui-sidenav .sui-vertical-tabs .sui-vertical-tab .sui-tag {
			font-size: 15px;
			font-weight: 500;
			line-height: 20px;
			letter-spacing: -0.25px;
			color: palette(gray, default);
		}

		.forminator-loading-state {
			display: flex;
			padding: 0 0 0 15px;
			gap: 10px;
			align-items: center;

			@include media(max-width, lg) {
				padding: 0;
			}
		}

		// template cards
		.forminator-template-cards {
			margin: -30px;

			.forminator-card-image {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				margin: 5px;
				padding-top: 60.61%;
				overflow: hidden;
				border-radius: 4px;

				.forminator-template-icon,
				img {
					position: absolute;
				}

				img {
					top: 0;
					height: 100%;
				}

				.forminator-template-icon {
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}
			}

			.sui-box-selectors {
				&.sui-box-selectors-col-3 {
					ul {
						li {
							@include media(min-width, sm) {
								min-width: 50%;
								flex-basis: 50%;
							}

							@include media(min-width, lg) {
								min-width: 33.33%;
								flex-basis: 33.33%;
							}
						}
					}
				}
			}

			.forminator-card-details {
				position: relative;
				border-top: 1px solid rgba(230, 230, 230, 0.50);
				height: 138px;
				padding: 20px;

				.forminator-card-cta {
					opacity: 0;
					pointer-events: none;
					visibility: hidden;
					display: flex;
					flex-direction: column;
					gap: 10px;
					width: 100%;
					bottom: 0;
					left: 0;
					right: 0;
					padding: 0 20px 20px;
					position: absolute;
					background: $white;
					transition: ease all 0.2s;

					.sui-button {
						width: 100%;
					}
				}

				h3 {
					color: palette(gray, default);
					text-overflow: ellipsis;
					text-wrap: nowrap;
					overflow: hidden;
					font-size: 15px;
					font-weight: 500;
					line-height: 22px;
					letter-spacing: -0.2px;
					margin: 0 0 10px;
				}

				p {
					font-size: 13px;
					line-height: 22px;
					letter-spacing: -0.25px;
					color: palette(gray, default);
					margin-top: 10px;
					overflow: hidden;
					display: -webkit-box;
					display: -moz-box;
					display: box;
					-webkit-box-orient: vertical;
					-moz-box-orient: vertical;
					box-orient: vertical;
					-webkit-line-clamp: 3;
					-moz-prefixed-line-clamp: 3; /* For Firefox */
					max-height: 66px; /* fallback for non-supporting browsers */
				}
			}

			.forminator-card-details.forminator-card-for-blank {
				.forminator-card-cta {
					justify-content: space-around;
					top: 50px;
				}
			}

			.forminator-card {
				position: relative;
				background: $white;
				border-radius: $border-radius;
				box-shadow: $box-selector--box-shadow;
				padding-top: 0.05px;

				&:hover {
					.forminator-card-details {

						.forminator-card-cta {
							opacity: 1;
							visibility: unset;
							pointer-events: all;
						}
					}
					.forminator-template-icon svg path {
						fill: #17A8E3;
					}
				}
			}
		}

		// Templates page notice
		.forminator-template-notice {
			padding: 15px 20px;
			border: 1px dashed $fiftyshades;
			background: $smoke;
			border-radius: 4px;
			text-align: center;
			margin: 10px 0;

			h2, p {
				font-size: 13px;
				line-height: 22px;
				max-width: 500px;
				margin: 0 auto;
			}

			a {
				text-decoration: underline;
			}
		}
    }
}