.sui-box {
	.forminator-dashboard-create-first-form,
	.forminator-dashboard-other-form-types {
		margin: 0 auto;
		max-width: 1000px;
		text-align: center;
	}
	.forminator-dashboard-create-first-form {
		border-bottom: 1px solid #D9D9D9;
		padding-bottom: 30px;
 	 	margin-bottom: 30px;

		.sui-box-title {
			&.forminator-create-form-title {
				font-size: 22px;
				margin-top: 10px;
			}
		}
		button#forminator_view_all_templates {
			padding: 10px 20px;
			font-size: 15px;
		}
	}
	.forminator-dashboard-no-form-block {
		padding: 60px 30px;
	}
	.forminator-dashboard-other-form-types {
		margin-top: 50px;
		.sui-box-title {
			&.forminator-other-form-title {
				font-size: 18px;
				font-weight: 500;
			}
		}
		.sui-row {
			margin-top: 30px;
		}
		.forminator-dashboard-other-form {
			border: 1px solid #e6e6e6;
			border-radius: 5px;
			&:hover,
			&:focus {
				border: 1px solid #AAAAAA;
			}
			h3.sui-box-title [class*="sui-icon-"]{
			  position: relative;
			  top: 4px;
			}
		    .sui-box-header {
				border: unset;
			}
			text-align: left;
			box-shadow: unset;
		}
	}
}