@include body-class(true) {

	.sui-checkbox {

		&.sui-checkbox-pre {
			font-family: monospace;

			input + span + span {
				word-break: break-all;
			}
		}
	}
}

// High contrast mode
@include body-class(true, false, true) {
	.sui-radio-image {
		
		img {
			filter: grayscale(1);
			-webkit-filter: grayscale(1);
		}
	}
}