@use "sass:math";

$fui-multi-options--background: palette(silver, light);

$fui-multi-options--button-height:       70px;
$fui-multi-options--button-height-md:    60px;
$fui-multi-options--button-border-width: 1px;
$fui-multi-options--button-border-style: dashed;
$fui-multi-options--button-border-color: palette(silver, medium);
$fui-multi-options--button-background:   transparent;
$fui-multi-options--button-color:        palette(gray, light);
$fui-multi-options--button-font-size:    12px;
$fui-multi-options--button-line-height:  16px;

$fui-multi-options--option-background: $white;

$fui-thumb-bg: #DDD;
$fui-scrollbar-bg: #f4f4f4;

@include body-class(true) {

	.multivalue-modal {

		div[id^="forminator-modal-body-"] {

			.sui-tab-content.active:not(:has(.sui-tooltip)) {
				overflow: hidden;
			}

			.sui-tab-content.active.forminator-modal-body--scrollable {
				overflow-y: auto;
			}
		}

		> .sui-modal-content {

			> .sui-select-theme--vars.select2-container--open {

				> .sui-select-dropdown {
					margin-left: 0;
					padding: 0;

					.select2-results__options {
						overflow-x: hidden;

						.select2-results__option {
							padding: 5px 14px;

							.sui-variable-name {
								color: #888;
								font: 500 15px/25px Roboto,Arial,sans-serif;
								letter-spacing: -0.25px;
								word-wrap: break-word;
							}

							.sui-variable-value {
								display: none;
							}
						}

						.select2-results__option--selected {

							.sui-variable-name {
								color: #fff;
							}
						}
					}

					& * {
						scrollbar-width: thin;
						scrollbar-color: $fui-thumb-bg $fui-scrollbar-bg;
					}

					& *::-webkit-scrollbar {
						width: 9px;
						height: 8px;
						background-color: $fui-scrollbar-bg;
					}

					& *::-webkit-scrollbar-track {
						background-color: $fui-scrollbar-bg;
					}

					& *::-webkit-scrollbar-thumb {
						background-color: $fui-thumb-bg;
						border-radius: 10px;
						cursor: default !important;
					}
				}
			}
		}
	}

	.fui-multi-options {

		.fui-add-option {
			width: 100%;
			cursor: pointer;
			display: block;
			margin: 0;
			border-width: $fui-multi-options--button-border-width;
			border-style: $fui-multi-options--button-border-style;
			border-color: $fui-multi-options--button-border-color;
			border-radius: $border-radius;
			background-color: $fui-multi-options--button-background;
			color: $fui-multi-options--button-color;
			font: 500 #{$fui-multi-options--button-font-size}/#{$fui-multi-options--button-line-height} $font;
			letter-spacing: $font--letter-spacing;
			text-transform: uppercase;
			text-align: center;

			&,
			&:hover,
			&:focus,
			&:active {
				outline: none;
				box-shadow: none;
			}

			[class*="sui-icon-"] {
				width: 23px;
				height: 16px;
				text-align: center;

				&:before {
					display: block;
					font-size: 12px;
					line-height: 16px;
				}
			}

			@include media(max-width, md) {
				height: $fui-multi-options--button-height-md;
				padding: #{math.div(($fui-multi-options--button-height-md - $fui-multi-options--button-line-height - ($fui-multi-options--button-border-width * 2)), 2)} #{math.div($sui-gutter-md, 2)};
			}

			@include media(min-width, md) {
				height: $fui-multi-options--button-height;
				padding: #{math.div(($fui-multi-options--button-height - $fui-multi-options--button-line-height - ($fui-multi-options--button-border-width * 2)), 2)} #{math.div($sui-gutter, 2)};
			}
		}

		.fui-option {
			cursor: move;
			display: flex;
			align-items: flex-start;
			margin: 0 0 10px;
			padding: 15px 10px;
			border-radius: $border-radius;
			background-color: $fui-multi-options--option-background;
			box-shadow: 0 2px 7px 0 rgba(162,138,138,0.05);

			@include icon(before, drag) {
				width: 30px;
				display: block;
				flex: 0 0 auto;
				margin: 12px 10px 12px 0;
				font-size: 16px;
				line-height: 16px;
				text-align: center;
			}

			&:hover,
			&:active,
			&.fui-on_focus {
				box-shadow: 0 0 0 4px rgba(0,0,0,0.02), 0 4px 15px 0 rgba(0,0,0,0.05);
			}

			.fui-option-settings {
				display: block;
				flex: 1;
				margin-right: 10px;

				[class^="fui-option-settings-"] {
					display: flex;
					align-items: center;
					cursor: default;

					> [class^="sui-"] {
						flex: 0 0 auto;
					}

					> .sui-form-field,
					> .sui-form-control {
						flex: 1;
						margin: 0;

						.sui-upload-file {

							> span {
								word-break: break-word;
							}
						}
					}

					> .sui-checkbox {

						> input {
							cursor: pointer;
						}
					}

					> *:not(:first-child) {
						margin-left: 10px;
					}

					~ [class^="fui-option-settings-"] {
						margin-top: 10px;
					}
				}
			}

			+ .fui-add-option {

				@include media(max-width, md) {
					margin-top: $sui-gutter-md;
				}

				@include media(min-width, md) {
					margin-top: $sui-gutter;
				}
			}
		}
		&.ui-sortable-disabled {
			.fui-option {
				cursor: default;
			}
		}

		&:not(.fui-nobg) {
			background-color: $fui-multi-options--background;

			@include media(max-width, md) {
				margin: 0 -#{$sui-gutter-md};
				padding: $sui-gutter-md;
			}

			@include media(min-width, md) {
				margin: 0 -#{$sui-gutter};
				padding: $sui-gutter;
			}

			&.standard-editor {
				position: relative;
				padding-right: 15px;
				padding-bottom: 0;
				border-right: 15px solid transparent;
				min-height: 0;
				max-height: 500px;
				overflow-y: auto;

				&.no-option {
					padding: 0;
					margin: 0 !important;
				}

				&::after {
					content: ' ';
					display: block;
					width: 100%;
					height: 30px;
				}
			}
		}

		&.add-option-box {
			position: relative;
			z-index: 100;
			padding-top: 30px;
			margin-top: 30px;
			box-shadow: 0px -10px 10px 0px rgba(0,0,0,0.05);
			-webkit-box-shadow: 0px -10px 10px 0px rgba(0,0,0,0.05);
			-moz-box-shadow: 0px -10px 10px 0px rgba(0,0,0,0.05);
		}

		&.custom-option {
			.fui-option {
				&::before {
					color: #DDDDDD;
				}
			}
		}
	}

	.sui-form-field:last-child {

		.fui-multi-options {

			&:not(.fui-nobg) {

				@include media(max-width, md) {
					margin-bottom: -#{$sui-gutter-md};
				}

				@include media(min-width, md) {
					margin-bottom: -#{$sui-gutter};
				}
			}
		}
	}

	.multi-options-bulk {
		position: relative;
		min-height: 0;
		padding-top: 20px;
		border-top: 1px solid #E6E6E6;
		z-index: 0;

		#label-options {
			padding-top: 10px;
		}

		.switch-box {
		    text-align: right;
    		padding-bottom: 10px;

			@include media(max-width, md) {
				text-align: left;
			}

			.sui-checkbox {
				margin-right: 30px;
			}
			.bulk-edit-toggle {
				display: inline-flex;
				margin-bottom: -6px;

				.sui-toggle-label {
					font-size: 13px;
				}
			}
		}

		#jump-bottom-or-top {
			position: fixed;
			bottom: 10px;
			right: 15px;
			z-index: 1000;
		}

		.multi-opts-preloader {
			padding: 108px 0;
			margin: 0 -30px;
			border-top: 1px solid #e6e6e6;
			text-align: center;

			.sui-icon-loader {
				display: inline-block;
				width: 100px;
			}
		}

		.fui-multi-options-bulk {

			#add-predefined {
				font-size: 12px;
				color: #17a8e3;
				width: 100%;
				padding-right: 0;

				.select2-container {
					width: 100% !important;
					border: none;
					text-align: right;

					.dropdown-handle {
						display: none;
					}

					.select2-selection--single {
						display: inherit;
						height: auto;
						padding: 0 5px 0 0;
						border: none;
						line-height: 1;
						font-size: 12px;
						font-weight: 500;
						color: #17a8e3;
						background-color: #fff;
						text-align: right;
					}
				}
			}

			.sui-insert-variables {

				> * {
					text-align: left;
				}

				#bulk-edit-textarea {
					padding-right: 0;
					white-space: pre;
					overflow-wrap: normal;
					font-weight: 400;
					line-height: 1.5;
				}
				/*.select-container {
					margin-right: 16px;
				}*/

				.bulk-editor-description {
					white-space: pre-line;
				}
			}

			.bulk-importer {

				.upload-csv-btn {
					margin-right: 10px;
				}

				.uploaded-csv-file {
					margin-right: 10px;
				}

				.importer-error {
					margin: 10px 0;
					text-align: left;
				}

				&> .sui-description {
					text-align: left;
				}

			}
		}

		.collapse-options.sui-checkbox {

			input {

				+ span {
					border-color: transparent !important;
					background-color: transparent !important;

					@include icon(before, arrows-expand, true);

					&:before {
						position: absolute;
						top: 0;
						left: 0;
						opacity: 1 !important;
					}
				}

				&:checked {

					+ span {

						@include icon(before, arrows-compress, true);
					}
				}
			}
		}

		.sui-accordion-item-header {

			.sui-upload-image {

				.sui-image-preview {
					cursor: default;

					&:before {
						opacity: 0;
					}
				}
			}
		}

		& * {
			scrollbar-width: thin;
			scrollbar-color: $fui-thumb-bg $fui-scrollbar-bg;
		}

		& *::-webkit-scrollbar {
			width: 9px;
			height: 8px;
		  	background-color: $fui-scrollbar-bg;
		}

		& *::-webkit-scrollbar-track {
		  	background-color: $fui-scrollbar-bg;
		}

		& *::-webkit-scrollbar-thumb {
			background-color: $fui-thumb-bg;
			border-radius: 10px;
			cursor: default !important;
		}
	}
}