@include body-class(true) {

	.sui-box {

		.fui-builder-section {

			&--insert {
				display: flex;
				justify-content: space-between;
				border-bottom-width: 0;

				@include media(max-width, md) {
					padding-top: $sui-gutter-md;
					padding-bottom: $sui-gutter-md;
				}

				@include media(min-width, md) {
					padding-top: $sui-gutter;
					padding-bottom: $sui-gutter;
				}

				+ .fui-builder-section--pagination {
					border-top: 1px solid palette(silver, soft);
				}
			}
		}
	}

	#forminator-builder-content {
		padding: 30px;
		box-shadow: inset 0px 3px 4px rgba(0,0,0,.05);

		> .sui-notice {
			margin-bottom: 30px;
		}
	}
}

@include body-class( false ) {
	&.forminator-pro_page_forminator-cform-wizard,
	&.forminator-pro_page_forminator-poll-wizard,
	&.forminator-pro_page_forminator-knowledge-wizard,
	&.forminator-pro_page_forminator-nowrong-wizard,
	&.forminator_page_forminator-cform-wizard,
	&.forminator_page_forminator-poll-wizard,
	&.forminator_page_forminator-knowledge-wizard,
	&.forminator_page_forminator-nowrong-wizard {
		#wpcontent {
			padding: 0;
		}

		#wpbody-content {
			padding-bottom: 0;
		}

		.sui-row-with-sidenav {
			margin-bottom: 0;
		}

		.sui-wrap:not(.sui-modal) {
			margin: 0;
		}
		.sui-box {

			&.sui-box-sticky {
				margin-bottom: 0;
				position: sticky !important;
				top: 32px;
				z-index: 13;
			}
		}
	}
}

@media (max-width: 600px) {

	@include body-class( false ) {
		&.forminator-pro_page_forminator-cform-wizard,
		&.forminator-pro_page_forminator-poll-wizard,
		&.forminator-pro_page_forminator-knowledge-wizard,
		&.forminator-pro_page_forminator-nowrong-wizard,
		&.forminator_page_forminator-cform-wizard,
		&.forminator_page_forminator-poll-wizard,
		&.forminator_page_forminator-knowledge-wizard,
		&.forminator_page_forminator-nowrong-wizard {

			.sui-box {
				&.sui-box-sticky {
					top: 0px;
				}
			}
		}
	}
}

@media (max-width: 1249px) {

	@include body-class(true) {
		#forminator-builder-content {
			padding: 15px;

			> .sui-notice {
				margin-bottom: 15px;
			}
		}
	}
}