@use "sass:math";

@include body-class() {

    #forminator-addons {

        .sui-tabs-menu {
            border-top: 0;
            padding: 0 $sui-gutter-md;
            border-radius: $border-radius;
            background-color: $white;
            box-shadow: 0 2px 0 0 #E6E6E6;

            .sui-tab-item {
                padding-top: 24px;
                padding-bottom: 22px;
                font-size: 12px;

                .sui-tag {
                    margin-left: 4px;
                }
            }

            .sui-tab-search {
                cursor: default;
                margin-right: 0;
                margin-left: auto;
            }

            @include media(min-width, lg) {
                padding: 0 $sui-gutter;
            }
        }

        .sui-tabs-content {

            .sui-tab-content {

                .forminator-addon-card {
                    overflow: hidden;
                    display: block;
                    border-radius: $border-radius;

                    .forminator-addon-card--body {
                        padding: $sui-gutter;

                        .forminator-addon-card--body-left {

                            .forminator-addon-card--thumb {
                                width: 60px;
                                height: 60px;
                                display: block;
                                border-radius: $border-radius;
                                background-repeat: no-repeat;
                                background-size: cover;
                                background-position: center;
                            }

                            + .forminator-addon-card--body-right {

                                @include media(max-width, lg) {
                                    margin-top: #{math.div($sui-gutter-md, 2)};
                                }

                                @include media(min-width, lg) {
                                    margin-left: $sui-gutter-md;
                                }
                            }

                            @include media(max-width, lg) {
                                width: 60px;
                                margin: 0 auto;
                            }

                            @include media(min-width, lg) {
                                flex: 0 0 auto;
                            }
                        }

                        .forminator-addon-card--body-right {

                            .forminator-addon-card--title {
                                margin: 0 0 10px;

                                h3 {
                                    margin: 0;
                                    color: #333;
                                    font-size: 15px;
                                    line-height: 22px;

                                    + .forminator-addon-card--tags {

                                        .sui-tag-grey {
                                            background-color: #888;
                                            color: #fff;
                                        }

                                        @include media(max-width, lg) {
                                            margin-top: 10px;
                                        }

                                        @include media(min-width, lg) {
                                            margin-left: 8px;
                                        }
                                    }
                                }

                                .forminator-addon-card--tags {
                                    display: flex;
                                    flex-flow: row wrap;
                                    align-items: center;

                                    .sui-tag {
                                        display: block;
                                        flex: 0 0 auto;

                                        + .sui-tag {
                                            margin-left: 10px;
                                        }
                                    }

                                    @include media(max-width, lg) {
                                        justify-content: center;
                                    }

                                    @include media(min-width, lg) {
                                        min-width: 0;
                                        flex: 1;
                                    }
                                }

                                @include media(min-width, lg) {
                                    display: flex;
                                    flex-flow: row wrap;
                                    align-items: center;
                                    margin-bottom: 5px;
                                }
                            }

                            .sui-description {
                                margin: 0;
                            }

                            @include media(max-width, lg) {
                                text-align: center;
                            }

                            @include media(min-width, lg) {
                                min-width: 1px;
                                flex: 1;
                            }
                        }

                        @include media(min-width, lg) {
                            display: flex;
                            flex-flow: row wrap;
                            align-items: flex-start;
                        }
                    }

                    .forminator-addon-card--footer {
                        display: flex;
                        flex-flow: row wrap;
                        align-items: center;
                        justify-content: space-between;
                        padding: #{math.div($sui-gutter-md, 2)} $sui-gutter-md;
                        border-top: 1px solid #E6E6E6;
                        background-color: #F8F8F8;

                        .forminator-pseudo-link {
                            cursor: pointer;
                            margin: 0;
                            padding: 0;
                            border: 0;
                            background-color: transparent;
                            color: #17A8E3;
                            font: 500 13px/22px $font;

                            &:hover,
                            &:focus {
                                color: #1286B5;
                                outline: none;
                                box-shadow: none;
                            }
                        }

                        .sui-button {

                            &.sui-button-compound {

                                .sui-compound-desktop {

                                    @include media(max-width, lg) {
                                        display: none;
                                    }

                                    @include media(min-width, lg) {
                                        display: block;
                                    }
                                }

                                .sui-compound-mobile {

                                    [class*="sui-icon-"] {

                                        @include media(max-width, lg) {
                                            margin-right: 0;
                                            margin-left: 0;
                                        }
                                    }

                                    @include media(max-width, lg) {
                                        display: block;
                                    }

                                    @include media(min-width, lg) {
                                        display: none;
                                    }
                                }

                                @include media(max-width, lg) {
                                    min-width: 44px;
                                    padding-right: 5px;
                                    padding-left: 5px;
                                }
                            }
                        }

                        .forminator-addon-card--footer-left {
                            flex: 0 0 auto;

                            .sui-description {
                                font-size: 12px;
                                font-weight: 500;
                            }

                            + .forminator-addon-card--footer-right {
                                margin-left: $sui-gutter-md;
                            }
                        }

                        .forminator-addon-card--footer-right {
                            min-width: 1px;
                            flex: 1;
                            text-align: right;

                            $connectColor: #286EFA;
                            .pdf-connect {
                                background-color: $connectColor;

                                &:hover {
                                    background-color: darken( $connectColor, 18% );
                                }
                            }
                        }

                        @include media(min-width, lg) {
                            padding: #{math.div($sui-gutter, 2)} $sui-gutter;
                        }
                    }
                }
            }
        }
    }
}