@use "sass:math";

@include body-class(true) {

	#forminator-deactivation-survey-modal {

		.sui-box-header {

			.sui-box-title {
				display: flex;
				align-items: center;
			}

			.forminator-header-logo {
				display: flex;
				margin-right: 10px;
				height: 30px;
				width: 30px;
				background-size: contain;
				background-repeat: no-repeat;
				flex-shrink: 0;

				&:not(.sui-unbranded) {

					&:not(.sui-rebranded) {
						background-image: url(../../assets/images/forminator-summary@2x.png);
					}
				}

				&.sui-unbranded,
				&.sui-rebranded {
					display: none;
				}
			}

			.forminator-dismiss-deactivation-survey {
				top: 15px;
			}
		}

		.sui-box-body {

			p {
				font-size: 13px;
				line-height: 22px;
			}

			.forminator-deactivation-survey-option {
				margin-bottom: 20px;
				line-height: normal;

				&:last-child {
					margin-bottom: 0;
				}

				label {
					font-weight: 500;
					font-size: 13px;
					line-height: 22px;

					input {
						margin-right: 10px;
					}
				}

				.forminator-deactivation-survey-message {
					padding-left: 24px;
					padding-top: 10px;

					input {
						height: 30px;
						font-size: 12px;
						line-height: 16px;
						padding: 7px 12px;
					}
				}
			}
		}

		.sui-box-footer {
			padding: 24px 30px;
		}
	}
}
