@include body-class(true) {

	.fui-subdesign-options {

		.fui-subdesign-option {
			cursor: default;
			pointer-events: none;
			display: block;
			margin: 0 0 $sui-gutter-md;

			&--wrapper {
				overflow: hidden;
				display: block;
				border: 1px solid #DDD;
				border-radius: $border-radius;
				transition: 0.2s ease;
			}

			.fui-demo-form {
				padding: $sui-gutter-md;
				border-bottom: 1px solid #DDD;
				transition: 0.2s ease;

				&, span {
					display: block;
				}

				&--field {
					margin: 0 0 20px;

					&:last-child {
						margin-bottom: 0;
					}
				}

				&--label {
					margin: 0 0 5px;
					color: #777771;
					font: bold 12px/1.7em $font;
				}

				&--input {
					overflow: hidden;
					display: block;
					border-style: solid;
					border-color: #777771;
					background-color: #EDEDED;
					color: #AAA;
					font: 400 16px/1.3em $font;
					white-space: nowrap;

					&.fui-is_filled {
						color: #000;
					}
				}

				&[data-design="default"] {

					.fui-demo-form--input {
						padding: 9px;
						border-width: 1px;
						border-radius: 2px;
					}
				}

				&[data-design="flat"] {

					.fui-demo-form--input {
						padding: 10px;
						border-width: 0;
					}
				}

				&[data-design="bold"] {

					.fui-demo-form--input {
						padding: 7px;
						border-width: 3px;
					}
				}

				&[data-design="material"] {

					.fui-demo-form--input {
						margin: 25px 0 0;
						padding: 10px 0 9px;
						border-width: 0 0 1px 0;
						background-color: transparent;
						text-overflow: ellipsis;

						&.fui-is_filled {
							margin: 0;
						}
					}
				}
			}

			.sui-radio {
				padding: $sui-gutter-md;
			}

			&:last-child {
				margin-bottom: 0;
			}

			&.fui-selected {

				.fui-subdesign-option {

					&--wrapper {
						border-color: $blue;
					}
				}

				.fui-demo-form {
					border-bottom-color: $blue;
				}

				.sui-radio {
					background-color: #E1F6FF;
				}
			}

			&:not(.fui-selected) {

				.fui-subdesign-option {

					&--wrapper {
						cursor: pointer;
						pointer-events: initial;
					}
				}

				&:hover {

					.fui-subdesign-option {

						&--wrapper {
							border-color: #AAA;
							box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.05);
						}
					}

					.fui-demo-form {
						border-bottom-color: #AAA;
					}
				}
			}

			@include media(min-width, md) {
				width: 50%;
				min-width: #{$sui-gutter + 190px};
				display: block;
				flex: 1 1 auto;
				margin: 0;
				padding: #{$sui-gutter / 2};
			}

			@include media(min-width, lg) {
				width: 25%;
			}
		}

		@include media(min-width, md) {
			display: flex;
			flex-flow: row wrap;
			margin: -#{$sui-gutter / 2} !important;
		}
	}
}