@include body-class(true) {

	.sui-form-field-inline.forminator-fields-line-2x {
		@include media(min-width, md) {
			display: inline-flex;
			&:first-child {
				margin-right: 20px;
			}
		}
	}

	.fui-datepicker-tags {
		position: relative;

		> .sui-input {
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			position: absolute;
			bottom: 0;
			left: 0;
		}

		select.sui-select + .select2-container {

			.select2-selection--multiple {

				.select2-selection__rendered {
					display: flex;
					flex-direction: row;
					flex-wrap: wrap;
					align-items: flex-start;
					margin: -5px;

					.select2-search {
						margin: 5px;

						&.select2-search--inline {
							min-width: 100px;
							flex: 1;

							.select2-search__field {
								height: 24px;
							}
						}

						&:not(.select2-search--inline) {
							flex: 0 0 100%;
						}
					}

					.select2-selection__choice {
						flex: 0 0 auto;
					}
				}
			}
		}

		.fui-daterange-calendar {

			.daterangepicker {
				position: relative;
				top: 0 !important;
				left: 0 !important;

				&:before,
				&:after {
					content: " ";
					display: table;
				}

				&:after {
					clear: both;
				}
			}
		}
	}
}