@use "sass:math";

@include body-class(true) {

	.sui-accordion-block {

		.sui-accordion-item {

			.sui-accordion-item-header {

				.sui-accordion-item-title {

					.sui-checkbox {

						@include media(max-width, md) {
							margin-right: #{math.div($sui-gutter-md, 2)};
							margin-bottom: 0;
							margin-left: -9px;
						}
					}

					@include media(max-width, md) {
						font-size: 18px;
					}
				}
			}

			.sui-accordion-item-body {

				.sui-accordion-item-data {
					align-items: center;
					.sui-button {
						position: relative;
						margin-left: 40px;
						&:after {
							content: '';
							position: absolute;
							left: -28px;
							top: 0;
							bottom: 0;
							width: 2px;
							background: #C4C4C4;
							border-radius: 1px;

							@include media(max-width, md) {
								content: unset;
							}
						}

						@include media(max-width, md) {
							margin-left: 0;
						}
					}
				}
			}
		}

		.forminator-notice-yellow {
			box-shadow: inset 2px 0 0 0 #fecf2f;
			.forminator-accordion-notice-icon {
				font-size: 15px;
				width: 28px;
			}
			.forminator-accordion-notice-icon::before {
				color: #fecf2f;
			}
		}
	}

	.sui-box-search {

		.sui-search-left {

			.sui-checkbox {
				display: inline-flex;
				margin-bottom: 0;

				@include media(max-width, md) {
					margin-right: #{math.div($sui-gutter-md, 2)};
				}
			}
		}
	}

	.fui-listings-pagination {

		.fui-pagination-mobile {
			justify-content: flex-end;
			margin-bottom: #{math.div($sui-gutter-md, 2)};

			.sui-pagination-results {
				display: block;
				margin-right: #{math.div($sui-gutter, 2)};

				@media ( max-width: 1112px ) {
					flex: 0 1 auto;
				}
			}

			.sui-pagination {

				li {

					@media ( max-width: 1112px ) {
						min-width: 30px;
						flex: unset;
					}

					a {

						@media ( max-width: 1112px ) {
							min-width: 30px;
						}
					}
				}

				@media ( max-width: 1112px ) {
					flex: 0 0 auto;
					margin-left: 0;
				}
			}

			@media ( min-width: 1113px ) {
				display: none;
			}
		}

		.fui-pagination-desktop {

			.sui-pagination {

				li {
					flex: unset;
					min-width: 40px;
				}
			}


			.sui-search-right {

				@media ( max-width: 1112px ) {
					display: none;
				}
			}
		}

		@include media(max-width, md) {
			margin-bottom: $sui-gutter-md;
		}

		@include media(min-width, md) {
			margin-bottom: $sui-gutter;
		}
	}
}