@include body-class(true) {

	.fui-integrations-block {

		.sui-table-title {
			display: block;
			margin: 0 0 5px;
			color: palette(gray, light);
			font: bold 12px/22px $font;
			letter-spacing: $font--letter-spacing;

			+ .sui-notice {
				margin: 5px 0 0;
			}
		}

		.fui-table--apps {
			margin: 5px 0;
		}

		&:not(:last-child) {

			@include media(max-width, md) {
				margin: 0 0 $sui-gutter-md;
			}

			@include media(min-width, md) {
				margin: 0 0 $sui-gutter;
			}
		}
	}

	#forminator-integration-popup {

		.forminator-select-refresh {
			display: flex;
			flex-flow: row nowrap;
			align-items: center;

			span.sui-select {
				min-width: 1px;
				flex: 1;
			}

			.forminator-refresh-email-lists {
				flex: 0 0 auto;
				margin-left: 10px;
			}
		}

		.sui-insert-variables {

			textarea {

				& ~ span {

					.select2-selection {
						border-left: none;
					}
				}
			}
		}
	}
}