@use "sass:math";

// DIALOG: Stripe SCA Compliant
@include body-class(true) {

	// Insert fields.
	#modal-insert-fields {
		input+span {
			font-size: 13px;
		}
		input:not(:checked)+span {
			color: #333;
			i:before {
				color: #888;
			}
		}
		p {
			color: #333;
		}
	}

	.sui-modal {
		&.sui-modal-xxl .sui-modal-content {
			max-width: 1260px;
		}

		overflow-wrap: break-word;

		// FIX: Center align loader
		.fui-loading-modal {
			text-align: center;
		}

		.fui-section-toggle {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: #{$sui-gutter-md - 1px} $sui-gutter-md;
			background-color: palette(silver, light);

			.sui-toggle {
				flex: 0 0 auto;
			}

			.sui-toggle-label {
				min-width: 1px;
				flex: 0 1 auto;
				color: palette(gray, default);
				font-size: 13px;
				line-height: 22px;
				font-weight: 500;
			}

			@include media(max-width, md) {
				margin-right: -#{$sui-gutter-md};
				margin-left: -#{$sui-gutter-md};
			}

			@include media(min-width, md) {
				margin-right: -#{$sui-gutter};
				margin-left: -#{$sui-gutter};
				padding-right: $sui-gutter;
				padding-left: $sui-gutter;
			}
		}

		.fui-section-toggle-content {

			.sui-error-message {
				text-align: right;
			}

			.sui-form-field-error {

				.sui-insert-variables {

					.select-container {

						.select-list-container {

							.list-value {
								border-bottom-color: $red;
							}
						}
					}
				}
			}

			@include media(max-width, md) {
				margin-top: $sui-gutter-md;
			}

			@include media(min-width, md) {
				margin-top: $sui-gutter;
			}
		}

		.fui-dismiss-box {
			background-color: #FAFAFA;

			.fui-dismiss-list {
				margin: 0 0 #{math.div($sui-gutter, 2)};

				&, li {
					padding: 0;
					border: 0;
					list-style: none;
				}

				li {
					color: palette(gray, light);
					font-size: 13px;
					line-height: 22px;

					strong {
						color: palette(gray, default);
					}
				}

				&:last-child {
					margin: 0;
				}
			}

			.fui-dismiss-button {
				width: auto;
				max-width: 100%;
				cursor: pointer;
				opacity: 0.5;
				display: block;
				margin: 0;
				padding: 0;
				border: 0;
				border-radius: 0;
				background-color: transparent;
				color: palette(gray, dark);
				font: bold 12px/22px $font;
				text-transform: uppercase;
				transition: $transition;

				[class*="sui-icon-"] {
					vertical-align: middle;
					margin-right: 3px;
				}

				&:hover,
				&:focus {
					opacity: 1;
				}

				&:last-child {
					margin-bottom: -5px;
				}
			}

			&.fui-flushed {

				@include media(max-width, md) {
					margin: 0 -#{$sui-gutter-md};
					padding: #{$sui-gutter-md};
				}

				@include media(min-width, md) {
					margin: 0 -#{$sui-gutter};
					padding: #{$sui-gutter-md} #{$sui-gutter};
				}
			}
		}

		.fui-addons-option {
			margin-bottom: $sui-gutter-md;

			&:last-child {
				margin-bottom: 0;
			}
		}

		.sui-modal-content {

			> .sui-select {
				z-index: 1;

				.select2-results__option {

					&.select2-results__option--group {
						display: block !important;
					}
				}
			}

			.sui-upload {

				.sui-upload-file {
					
					span {
						word-break: break-all;
					}
				} 
			} 
		}

		// BLOCK: Description.
		.sui-description {
			strong {
				color: $blue;
			}

			&.sui-description-alt {
				margin-top: 10px;

				@include media(max-width, md) {
					margin-top: -10px;
				}
			}
		}

		// Fixed height for modals with scrollable body
		.forminator-modal-body--scrollable {
			max-height: 60vh;
			overflow-y: auto;
		}
	}

	#forminator-stripe-sca {

		.sui-box {

			.sui-box-header {

				.sui-dialog-close {
					color: $white;

					&:before {
						color: inherit;
					}

					&:focus,
					&:hover,
					&:active {
						opacity: 0.7;
					}
				}
			}

			.sui-box-body {

				@include media(max-width, md) {
					padding-bottom: #{$sui-gutter-md * 2};
				}

				@include media(min-width, md) {
					padding-bottom: #{$sui-gutter * 2};
				}
			}
		}
	}

	#forminator-modal {
		.sui-box-header {
			.fui-field--icon {
				display: inline-block;
				i {
					&:before {
						font-size: 16px;
						color: #666666;
						vertical-align: text-top;
					}
				}
			}
			.sui-box-title {
				overflow: unset;
			}
		}
		
		.fp-role-img {
			display: inline-block;
			margin-right: 10px;
			overflow: hidden;
			border-radius: 50%;
			width: 30px;
			height: 30px;
		}
	}

	#forminator-new-feature {

		.sui-dialog-content {

			.sui-box-header {

				.sui-dialog-close {
					z-index: 2;

					&:before {
						color: white;
					}

					&:focus,
					&:hover,
					&:active {
						opacity: 0.7;
					}
				}

				.sui-box-title,
				.sui-description {

					&[sui-content-size="380"] {
						max-width: 380px;
						margin-right: auto;
						margin-left: auto;
					}
				}

				.sui-box-title {
					text-overflow: unset;
					white-space: normal;
				}
			}

			.sui-box-body {

				ul.sui-list {

					&[sui-type="bullets"] {
						display: block;
						margin: 0;
						padding: 0;
						border: 0;

						li {
							display: block;
							position: relative;
							padding: 0 0 0 17px;
							border: 0;

							.sui-description {
								margin: 0 0 5px;

								strong {
									color: #666;
									font-weight: 500;

									&[sui-color="darkgray"] {
										color: palette(gray, dark);
									}
								}

								&:last-child {
									margin-bottom: 0;
								}
							}

							&:before {
								content: " ";
								width: 7px;
								height: 7px;
								display: block;
								position: absolute;
								top: 8px;
								left: 0;
								border-radius: 7px;
								background-color: palette(gray, dark);
							}

							@include media(max-width, md) {
								margin: 0 0 $sui-gutter-md;
							}

							@include media(min-width, md) {
								margin: 0 0 $sui-gutter;
							}
						}
					}
				}

				&[sui-spacing-bottom="0"] {
					padding-bottom: 0 !important;
				}
			}

			.sui-box-footer {
				padding-top: 0;

				&[sui-space-bottom="60"] {

					@include media(min-width, md) {
						padding-bottom: 60px;
					}
				}
			}
		}
	}

	.sui-modal-list {
		h3, 
		ol {
			font-size: 13px;
			line-height: 22px;
			text-align: left;
			letter-spacing: -0.25px;
		}

		h3 {
			color: #666;
			font-weight: 700;
		}
		
		ol {
			color: #888;
			list-style: none;
			margin: 0;
	
			li {
				display: flex;
				margin: 10px 0;
			}

			.sui-icon-check {
				margin-right: 10px;
			}
		}

		.sui-tag {
			font-size: 8px;
			font-weight: 900;
			line-height: 6px;
			letter-spacing: -0.25px;
			text-transform: uppercase;
			margin-left: 8px;
			height: 8px;
			margin-top: 5px;
			padding: 2px 6px;
			min-height: 13px;
		}
	}
}

@include body-class($wrap: true, $rtl: false, $monochrome: true) {

	.sui-modal {
		// BLOCK: Description.
		.sui-description {
			strong {
				color: $black;
				text-decoration: underline;
			}
		}
	}
}