@use "sass:math";

@include body-class(true) {

	.forminator-hub-modal {

        .sui-box {

            .sui-box-body {

                .sui-box-body-item {

					&.forminator-hub-modal-item {
						padding: 10px 15px;
						border: 1px solid palette(monos, silver);
						border-radius: 8px;

						&:last-child {
							margin-top: 10px;
						}
					}

					.forminator-hub-modal-box {
						display: flex;
						align-items: center;
						flex-wrap: wrap;
						gap: 10px;
						color: palette(mono, default);

						> :last-child {
							margin-left: auto;

							@include media(max-width, sm) {
								margin-left: 0;
							}
						}
					}

					.forminator-hub-modal-list {
						margin: 0;
						margin-top: 10px;
						padding: 10px 20px 0;
						border-top: 1px solid palette(monos, silver);

						li {
							display: flex;
							align-items: center;
							gap: 10px;
							margin-bottom: 5px;
							color: palette(gray, dark);
						}
					}
				}

				&.sui-flatten {

					.sui-notice {

						.sui-notice-content {
							background-color: #d1f1ea;
							border: 1px solid #1ABC9C;
							box-shadow: inset 0 0 0 1px #d1f1ea;
							padding: 13px 20px;

							.sui-notice-message {
								padding: 0;
								text-align: left;

								ul {
									margin: 8px 0;

									li {
										font-size: 13px;
										line-height: 22px;
										margin-bottom: 8px;
										color: #333;
										vertical-align: middle;
										width: 100%;

										&:last-child {
											margin-bottom: 0;
										}

										&::before {
											color: #1abc9c;
											margin-right: 8px;
											line-height: 22px;
											vertical-align: bottom;
										}
									}
								}
							}
						}
					}
				}
            }
        }
    }
}