@include body-class(true) {

	#forminator-modal-body--postdata {

		> .sui-tabs {

			> .sui-tabs-content {

				> .sui-tab-content {

					.fui-modal-accordion {
						margin-bottom: -#{$sui-gutter-md};

						@include media(min-width, md) {
							margin-bottom: -#{$sui-gutter};
						}
					}

					> .sui-box-settings-row {

						p:last-of-type {
							margin-bottom: 20px;

							&:last-child {
								margin-bottom: 0;
							}
						}

						.sui-form-field {

							&.fui-reduce-spacing {
								margin-top: 0;
								margin-bottom: $sui-gutter-md;

								&:last-child {
									margin-bottom: 0;
								}
							}
						}
					}
				}
			}
		}
	}

	#pdf-payment-modal.sui-accordion.sui-accordion-flushed.fui-modal-accordion-flushed {
		margin: -#{$sui-gutter-md};

		@include media(min-width, md) {
			margin: -#{$sui-gutter};
		}
	}
}