@use "sass:math";

@include body-class(true) {

	.sui-header {

		&.fui-header-with-settings {
			display: block;

			.fui-header-title {
				display: flex;
				justify-content: space-between;
				flex-direction: column;

				.sui-header-title {
					min-width: 20px;
					flex: 1;
				}

				.sui-actions-right {
					flex: 0 0 auto;
					margin-top: 5px;
					margin-bottom: 5px;

					@include media(min-width, md) {
						display: none;
					}
				}

				@include media(min-width, md) {
					flex: 0 0 auto;
					padding-right: #{math.div($sui-gutter-md, 2)};
				}

				@include media(min-width, lg) {
					min-width: 220px;
					padding-right: $sui-gutter;
				}
			}

			.fui-header-settings {
				display: block;

				.sui-actions-left {
					display: flex;
					align-items: flex-start;

					.sui-form-field {
						min-width: 100px;
						flex: 1;
						margin-right: 10px;
						margin-bottom: 0;

						@include media(min-width, md) {
							min-width: 240px;
							flex: 0 0 auto;
						}
					}

					.sui-dropdown,
					.sui-button-icon[data-tooltip] {
						flex: 0 0 auto;
						margin: 5px 5px 5px 0;

						&:last-child {
							margin-right: 0;
						}
					}

					.sui-dropdown {

						ul {
							z-index: 13;
						}
					}

					.sui-tooltip {

						&:before,
						&:after {
							z-index: 13;
						}
					}

					@include media(min-width, md) {
						flex: 1;
						margin-left: 0;
					}
				}

				.sui-actions-right {
					display: none;

					@include media(min-width, md) {
						display: block;
						margin-left: 10px;
						padding: 5px 0;
					}
				}

				@include media(min-width, md) {
					min-width: 300px;
					display: flex;
					align-items: flex-start;
					flex: 1;
				}
			}

			@include media(min-width, md) {
				display: flex;
				align-items: flex-start;
			}
		}
	}
}