@use "sass:math";

@include body-class(true) {

	#forminator-disconnect-hub-modal {

		.sui-box {

			.sui-box-body {

				.sui-notice {
					margin: 0 0 20px;

					.sui-notice-content {
						background-color: #fffbf0;
						border: 1px solid #f9c101;
						box-shadow: inset 0 0 0 1px #e6e6e6;
						padding: 13px 20px;

						.sui-notice-message {
							padding: 0;

							ul {
								margin: 8px 0;

								li {
									font-size: 13px;
									line-height: 22px;
									margin-bottom: 8px;
									color: #333;
									vertical-align: middle;

									&:last-child {
										margin-bottom: 0;
									}

									&::before {
										color: #666;
										margin-right: 8px;
										line-height: 22px;
										vertical-align: bottom;
									}
								}
							}
						}
					}
				}

				h3 {
					font-size: 13px;
					line-height: 22px;
					margin: 0;
				}
			}
		}
	}
}
