@include body-class(true) {

	.sui-dropdown {

		&.fui-dropdown-soon {

			ul {
				width: auto;
				min-width: 180px;

				li {

					a, button {

						[class*="sui-icon-"] {
							margin-right: 0;
						}

						&.sui-tooltip {

							&.sui-tooltip-left {

								&:after {
									width: 174px;
									white-space: normal;
								}
							}
						}
					}
				}

				.fui-button-with-tag {
					display: flex;
					flex-direction: row;
					flex-wrap: nowrap;
					align-items: center;

					[class*="sui-icon-"] {
						display: block;
						flex: 0 0 auto;
					}

					.fui-button-label {
						min-width: 1px;
						overflow: hidden;
						display: block;
						flex: 1;
						white-space: nowrap;
						text-overflow: ellipsis;
					}

					.sui-tag {
						pointer-events: none;
						display: block;
						margin-left: 5px;

						&.sui-tag-sm {
							height: 12px;
							min-height: auto;
							display: block;
							padding: 1px 6px 4px;
							font-size: 8px;
							line-height: 1;
							text-transform: uppercase;
						}
					}
				}
			}
		}

		ul {
			li {
				a, button {
					&.forminator-field-remove.sui-option-red{
						&:focus {
							background-color: #ffe5e9;
						}
					}
				}
			}
		}
	}
}