$side-navigation-width: 360px;
$side-appearance-width: 400px;
$side-navigation-width-mobile: 60px;
@include body-class(true) {

	.sui-sidenav {

		&.fui-sidenav {

			ul.sui-vertical-tabs {
				margin-top: 0;

				li.sui-vertical-tab {
					padding: 0;

					a {
						padding: 5px 15px;
						border-radius: 20px;

						&.current {
							background-color: palette(silver, soft);
							color: palette(gray, dark);

							// Remove after SUI 2.12.25
							@media (forced-colors: active) {
								border: 1px solid palette(yellow, default);
							}
						}
					}

					.sui-tooltip {

						.sui-icon-lock.sui-sm {

							&::before {
								font-size: 12px;
							}
						}
					}
				}
			}
		}

		.sui-mobile-nav {
			max-width: none;
		}
	}

	.sui-row-with-sidenav {

		.sui-sidenav {

			@media (max-width: 1249px) {
				margin-bottom: 20px;
			}

			.sui-vertical-tabs {

				.sui-vertical-tab {

					a > .sui-tag {
						position: relative;
						top: -3px;
						right: auto;
						display: inline-block;
						font-size: 8px;
						line-height: 8px;
						min-height: 12px;
					}
				}
			}
		}
	}

	.sui-settings-label {

		.fui-tooltip-description {
			margin-left: 10px;
			cursor: help;
		}
	}

	// Style adjustments for right navigation.
	.sui-row-with-sidenav {

		#forminator-builder-content {

			>.sui-box {
				margin-bottom: 30px;
				overflow: auto;
			}
		}

		.sui-sidenav {

			&.forminator-side-navigation {
				width: $side-navigation-width;
				padding-right: 0;
				display: table-cell;

				ul.sui-vertical-tabs {

					li.sui-vertical-tab {
						margin: 0 0 1px;

						a {
							padding: 15px 20px;
							border-radius: 5px;
							display: flex;
							flex-direction: row;
							align-items: center;
							letter-spacing: 0;

							&.current,
							&:hover {
								background-color: #f1f1f1;

								span.forminator-navigation-icon {

									svg {
										fill: #444;
									}
								}
							}

							span.forminator-navigation-icon {
								margin-right: 10px;
								display: flex;
							}

							span.forminator-navigation-right-arrow {
								margin-left: auto;
								display: flex;
							}

							&.forminator-disabled-menu-item {
								background-color: unset;

								> * {
									color: #9C9C9C;
								}

								span.forminator-navigation-icon {

									svg {
										fill: #9C9C9C;
									}
								}

								.forminator-lock-icon {
									position: unset;
									margin-left: auto;
									display: flex;
								}
							}
						}
					}
				}

				.forminator-sidenav-right {
					border-radius: 0;
					width: $side-navigation-width;
					position: sticky !important;
					top: 93px;
					z-index: 12;

					.forminator-side-navigation-content {
						padding: 20px;
					}

					.forminator-sidenav-title {
						margin: 10px 20px 10px 10px;
						color: #888;
						font-size: 13px;
						text-transform: uppercase;
						font-weight: 500;
					}

					&.sui-sidenav-fixed {
						position: fixed !important;
						height: 100%;
						overflow-y: auto;
						overflow-x: hidden;
						border-left: 1px solid #e6e6e6;
						box-shadow: unset;

						@keyframes forminator-slide-in {
							from {
								transform: translateX(100%); /* Start off-screen to the right */
								opacity: 0;
							}
							to {
								transform: translateX(0); /* End at its natural position */
								opacity: 1;
							}
						}

						&.forminator-slide-in {
							animation: forminator-slide-in 0.3s ease-out forwards;
						}

						.forminator-slide-out {
							animation: forminator-slide-in 0.3s ease-out reverse forwards;
							position: absolute;
							background: #fff;
							z-index: 12;
							width: 100%;
							height: 100%;
						}
					}
				}

				.forminator-side-navigation-footer {
					position: fixed;
					bottom: 0;
					z-index: 12;
					background-color: #fff;
					width: $side-navigation-width;
					border-left: 1px solid #e6e6e6;
				}
			}
		}

		&.forminator-secondary-nav-is-active {

			.sui-sidenav {

				&:not(:has(.forminator-slide-out)) {
					width: $side-appearance-width;
				}

				&.forminator-side-navigation {

					.forminator-sidenav-right {
						padding: 0;

						&:not(:has(.forminator-slide-out)) {
							width: $side-appearance-width;

							.forminator-side-navigation-content{
								display: none;
							}
						}

						.forminator-secondary-nav-header {
							background-color: #f9f9f9;

							button.sui-button-icon {
								border-color: #ddd;
							}

							.sui-box-title {
								font-weight: 500;
							}
						}

						.forminator-secondary-nav-content {
							max-height: calc(100vh - 155px);
							overflow: auto;
						}

						.forminatior-nav-accordion-item {

							>.sui-accordion-item-header {
								cursor: pointer;
								padding: 15px 20px 15px 30px;
								display: block;

								.sui-accordion-item-title {
									font-size: 15px;
									font-weight: 500;
									color: #555;
									display: flex;
									line-height: 22px;
									align-items: center;

									button {
										margin-left: auto;
									}
								}
							}

							border-bottom: 1px solid #e6e6e6;

							>.sui-accordion-item-header {

								.sui-accordion-open-indicator {

									[class*="sui-icon-"] {
										transition: .2s linear;
									}
								}
							}

							&.sui-accordion-item--open {

								>.sui-accordion-item-header {

									.sui-accordion-item-title {
										color: #333;
									}

									.sui-accordion-open-indicator {

										[class*="sui-icon-"] {
											transform: rotate(180deg);
										}
									}
								}

								>.sui-accordion-item-body {
									padding: 0 20px 30px 30px;

									.sui-tab-content {

										&.sui-tab-boxed {
											padding: 20px;
										}
									}

									.sui-settings-label {
										&.sui-dark {
											font-weight: 500;
										}
									}

									.sui-description {
										margin-bottom: 10px;
									}

									.sui-accordion-item-title,
									.sui-accordion-header > div {
										padding: 0 20px;
									}

									.sui-accordion-item-body {
										padding: 0 20px 20px;

										.sui-box-body {
											padding: 30px 20px;

											> .sui-tabs-flushed {
												margin: -20px !important;
											}
										}

										.sui-tabs-content > .sui-tab-content {
											padding: 20px;
										}

										.sui-tabs-menu {
											margin: 0 20px;
										}
									}

									.sui-box-settings-row {
										border-bottom: 1px solid #e6e6e6;
										padding-bottom: 20px;
										margin-bottom: 20px;

										&:last-child {
											border-bottom: none;
											padding-bottom: 0;
											margin-bottom: 0;
										}
									}

									.sui-box {

										&:focus-within {

											&:has(.sui-box) {
												box-shadow: 0 2px 0 #e6e6e6;
  												border-radius: 4px;
											}
										}
									}

									#forminator-field-custom_css {
										height: 500px !important;
									}
								}
							}
						}
					}
				}
			}
		}
	}

	&.sui-color-accessible {

		.forminator-side-navigation {

			svg {
				fill: $accessible-dark !important;
			}
		}
	}
}

@media (max-width: 1249px) {

	@include body-class(true) {

		.sui-row-with-sidenav {

			#forminator-builder-content {

				>.sui-box {
					margin-bottom: 15px;
				}
			}

			.sui-sidenav {

				&.forminator-side-navigation {
					margin-bottom: 20px;
					width: $side-navigation-width-mobile;
					display: table-cell;

					.forminator-sidenav-right {
						width: $side-navigation-width-mobile;

						.forminator-sidenav-title {
							display: none;
						}

						.forminator-side-navigation-content {
							padding: 15px 5px 5px 5px;
						}

						&:has(.forminator-slide-out) {
							.forminator-side-navigation-content{
								display: none;
							}
						}
					}

					ul.sui-vertical-tabs {

						li.sui-vertical-tab {
							margin: 10px 5px 10px;

							&:has(.forminator-disabled-menu-item) {
								display: none;
							}

							a {
								padding: 0;

								:not(:first-child),
								span.forminator-navigation-right-arrow {
									display: none;
								}

								&.forminator-disabled-menu-item {

									.forminator-lock-icon {
										display: none;
									}
								}

								span.forminator-navigation-icon {
									margin-right: 0;

									svg {
										width: 40px;
										height: 40px;
									}
								}
							}
						}
					}

					.forminator-side-navigation-footer {
						width: $side-navigation-width-mobile;
					}
				}
			}

			&.forminator-secondary-nav-is-active {

				.sui-sidenav {
					width: $side-navigation-width;

					.forminator-sidenav-right {
						width: $side-navigation-width;
					}
				}
			}
		}
	}
}

@media (max-width: 768px) {

	@include body-class(true) {

		.sui-row-with-sidenav {

			&.forminator-secondary-nav-is-active {

				#forminator-builder-content {
					display: none;
				}

				.sui-sidenav {
					width: 100% !important;

					.forminator-sidenav-right {
						width: 100% !important;
					}
				}
			}
		}
	}
}

@media (max-width: 1100px) {

	@include body-class(true) {

		.sui-row-with-sidenav {
			display: table;
		}
	}
}

@include body-class() {

	> .sui-select {

		&.sui-select-dropdown-container--open {
			z-index: 100;
		}
	}
}