%forminator-icons {
	font-family: 'forminator-plugin-icons' !important; // Use !important to prevent issues with browser extensions that change fonts.
	speak: none;
	font-size: 1em;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	text-rendering: auto;
	display: inline-block;
	color: $icon-color;
	opacity: 1;

	// Better Font Rendering
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	@media all and (-ms-high-contrast: none) {
		color: $icon-color;
	}
	@media all and (-ms-high-contrast: active) {
		color: $icon-lighter-color;
	}
}

@mixin forminatoricon($position: before, $icon: false, $styles: true) {
	@if $position == both {
		$position: 'before, &:after';
	}
	// Either a :before or :after pseudo-element, or both, defaulting to :before.
	&:#{$position} {
		@if $icon {
			// A particular icon has been specified.
			content: "#{map-get($forminatoricons, $icon)}";
		}
		@if $styles {
			@extend %forminator-icons;
		}
		// Include any extra rules supplied for the pseudo-element.
		@content;
	}
}

// Map icon names to font unicode characters.
$forminatoricons: (
    group: "\e900",
    currency: "\e901",
    pagination: "\e902",
    paypal: "\e903",
    stripe: "\e904",
    conversion: "\e905",
    bounce: "\e906",
    payment: "\e907",
	star: "\e908",
	heart: "\e909",
	like: "\e90a",
	smile: "\e90b"
);


@font-face {
	font-family: 'forminator-plugin-icons';
	src: url('#{$forminator-font-path}forminator-plugin-icons.eot?#{$forminator-version}');
	src: url('#{$forminator-font-path}forminator-plugin-icons.eot?#{$forminator-version}') format('embedded-opentype'),
	url('#{$forminator-font-path}forminator-plugin-icons.ttf?#{$forminator-version}') format('truetype'),
	url('#{$forminator-font-path}forminator-plugin-icons.woff?#{$forminator-version}') format('woff'),
	url('#{$forminator-font-path}forminator-plugin-icons.woff2?#{$forminator-version}') format('woff2'),
	url('#{$forminator-font-path}forminator-plugin-icons.svg?#{$forminator-version}') format('svg');
	font-weight: normal;
	font-style: normal;
}

@include body-class(false) {

    // Set the required styles on all icons.
	[class*="forminator-icon-"] {
		display: inline-block;
		@include forminatoricon(both);
	}

	// Setup a class name for each icon.
	@each $name, $char in $forminatoricons {
		.forminator-icon-#{$name}:before {
			content: $char;
		}
	}
}
