@include body-class(true) {
	#forminator-modal-body--group .sui-side-tabs.forminator-float {

		> .sui-tabs-menu {
			width: 40%;
			float: left;
		}

		> .sui-tabs-content {
			width: 60%;
			display: inline-block;

			.sui-tab-content {
				margin: 0;
			}
		}

	}

	#forminator-builder-fields {
		[id^='field-group-'] {
			width: calc( 100% - 20px );

			.fui-builder-fields {
				width: calc( 100% - 20px );
			}
		}

		[data-field="group"] {
			flex-wrap: wrap;

			.fui-builder-fields {
				flex-basis: 100%;
				padding: 10px;
				margin: 0 20px 20px;
			}
		}
	}

	.fui-drop-view {
		position: fixed;
		z-index: 999;
	}
}