@include body-class(true) {
  #forminator-builder-status {

	&.sui-box-sticky {
		border-radius: 0;
		box-shadow: unset;
	}

	#forminator-button-back-to-form {
	  margin-right: 20px;
	}

	#forminator-module-save,
	#forminator-module-revert {
	  border-color: transparent;
	  min-width: 0;
	  margin-left: 5px;
	  padding: 5px;

	  [class^="sui-icon-"] {
		margin: 0;
	  }
	}

	dfn {
	  cursor: help;
	  --tooltip-width: 177px;
	}

	.sui-dropdown {
	  margin: 0;

	  .sui-dropdown-anchor.sui-button-ghost {
		min-width: 30px;
		padding: 5px;

		i {
		  margin: 0;
		  width: 15px;
		}
	  }
	}

	.sui-dropdown.open .sui-dropdown-anchor.sui-button-ghost {
	  &:hover, &:focus {
		background-color: #888;
		color: #fff;
	  }
	}

	&.sui-box-sticky {

		.sui-box-status {
			border-bottom: 1px solid #E6E6E6;

			.sui-status {
				align-items: center;
				margin-right: 20px;

				.sui-status-module {

					.sui-tag {
						margin-left: 0 !important;
					}
				}

				.sui-status-changes {
					text-wrap: nowrap;
					color: #666666;
				}
			}

			.forminator-header-title {
				display: flex;
				align-items: center;
				margin-right: 20px;

				.sui-actions-left {
					margin-left: 0;

					.sui-field-suffix {
						margin-right: 20px;
					}
				}

				#forminator-button-back-to-form {
					min-width: auto;
					padding: 5px 0;
					width: 30px;
					height: 30px;

					span {
						margin-left: 0;
					}
				}
			}

			.forminator-header-logo {
				display: flex;
				margin-right: 15px;
				height: 40px;
				width: 40px;
				background-size: contain;
				background-repeat: no-repeat;
				flex-shrink: 0;

				&:not(.sui-unbranded) {

					&:not(.sui-rebranded) {
						background-image: url(../../assets/images/forminator-summary@2x.png);
					}
				}

			&.sui-unbranded,
			&.sui-rebranded {
				display: none;
			}
		}

			h1.sui-header-title {
				font-size: 18px;
				font-weight: 700;
				line-height: 28px;
				margin-right: 20px;
			}

			.sui-actions {
				display: flex;
				align-items: center;
				margin-left: 0;

				.sui-dropdown {

					ul {
						min-width: 200px;

						li {
							margin-right: 15px;
							margin-left: 15px;

							&.forminator-more-option-group-ends {
								border-bottom: 1px solid #e6e6e6;
								margin-bottom: 5px;
								padding-bottom: 5px;
							}

							a,
							button {
								padding: 0;
							}
						}

					}
				}
			}
		}
	}
  }

}

@media (min-width: 1101px) {

	@include body-class(true) {

		#forminator-builder-status {

			&.sui-box-sticky {

				.sui-box-status {
					padding: 15px 30px;

					.sui-actions {
						display: flex;
						gap: 10px;

						button.sui-button {
							padding: 10px 20px;
							font-size: 15px;
							letter-spacing: 0;
							line-height: 16px;
							height: 40px;
							margin-right: 0;
							
							&:not(.sui-dropdown-anchor) {
								display: flex;
								align-items: center;
								flex-wrap: wrap;
							}

							&,
							.sui-loading-text {
								display: flex;
								gap: 10px;
								align-items: center;
								justify-content: center;
							}

							i,
							[class*="sui-icon-"] {
								font-size: 16px;
								top: unset;
							}
						}

						.sui-dropdown {

							button.sui-button {
								padding: 0;
								height: 40px;
   								width: 44px;
							}
						}
					}
				}
			}
		}
	}
}


@media (max-width: 1249px) {

	@include body-class(true) {

		#forminator-builder-status {

			&.sui-box-sticky {

				.sui-box-status {
					padding: 15px 20px;

					h1.sui-header-title {
						display: none;
					}
				}
			}
		}
	}
}

@media (max-width: 1100px) {

	@include body-class(true) {

		#forminator-builder-status {

			&.sui-box-sticky {

				.sui-box-status {
					display: flex;
					flex-wrap: wrap;

					.forminator-header-title {
						margin-right: 0;
						flex: 0 0 100%;

						.sui-actions-left {
							width: 100%;

							.sui-form-field {
								width: 100%;
							}
						}
					}

					.sui-status {
						display: inline-flex;
						margin-right: 15px;
						margin-top: 15px;
					}

					.sui-actions {
						display: inline-flex;
						margin-top: 15px;
						margin-left: auto;
					}
				}
			}
		}
	}
}
