@include body-class(true) {

	.hui-form-field-reduced {

		&:not(:last-child) {

			@include media(max-width, md) {
				margin-bottom: 10px;
			}
	
			@include media(min-width, md) {
				margin-bottom: 10px;
			}
		}
	}

	.forminator-custom-form {
		ul {
			list-style: disc;
			margin-left: 2em;
		}
	}
}