// Remove this css after SUI 2.12.21 release.
.sui-cross-sell-modules {

    [class*="sui-cross-"] {
        position: relative;

        > span {
            position: absolute !important;
            top: 50% !important;
            left: 50% !important;
            transform: translate(-50%, -50%);
            border: unset !important;
            border-radius: unset !important;
            box-shadow: none !important;
        }
    }
}