@use "sass:math";

@include body-class(true) {

	.fui-recipient {
		display: flex;
		flex-direction: row;
		align-items: center;
		margin: 0;
		padding: #{math.div($sui-gutter, 2) - 1px} #{$sui-gutter - 1px};
		border: 1px solid palette(silver, soft);
		border-radius: $border-radius;
		background-color: $white;

		> [class*="sui-icon"] {
			display: block;
			flex: 0 0 auto;

			&:before {
				display: block;
				color: palette(gray, dark);
			}

			+ p {
				margin-left: 10px !important;
			}
		}

		p {
			min-width: 1px;
			overflow: hidden;
			flex: 1;
			margin: 0 !important;
			padding: 0;
			border: 0;
			color: palette(gray, dark);
			font: 500 13px/22px $font;
			letter-spacing: $font--letter-spacing;
			white-space: nowrap;
			text-overflow: ellipsis;

			+ .sui-tooltip {
				margin-left: 0;
			}
		}

		.sui-tooltip {
			flex: 0 0 auto;
			margin-right: -4px;
		}
	}
}