// ELEMENT: Insert Variables.
// $sui-wrap: true | $rtl: false
@include body-class(true, false) {
	// variation: select2 outside the textarea
    .sui-variables-select-text {

        .sui-label-note {
            margin-left: 5px;
        }

        select.sui-variables {

            + .sui-select {
                top: -20px;
                line-height: 1;
                font-size: 12px;
                font-weight: 500;
                color: palette(blue, default);
                text-align: right;
                height: 14px;

                .selection {
                    background: palette(mono, white);
                    position: absolute;
                    right: 0;
                    width: 100px;

                    span[role=combobox] {
                        height: auto;
                        border: none;
                    }
                }
            }
        }
    }
}