@use "sass:math";

@include body-class(true) {
	.forminator-banner {
		.forminator-banner-content {
			padding: 20px;
			display: flex;
			gap: 20px;

			@media (max-width: 480px) {
				flex-direction: column;
			}

			.fui-image {
				min-width: 96px;
			}

			h2 {
				margin: 0;
			}

			p {
				margin-top: 0;
			}
		}
	}
}