@use "sass:math";

@include body-class(true) {

	select {
		max-width: none;
	}

	.fui-entries-bar {
		display: flex;

		.fui-bar-selectors {
			display: flex;

			&__module + span.sui-select {

				~ .select2 {
					margin-left: 10px;
				}

				@include media(max-width, md) {
					flex: 0 0 auto;
				}

				@include media(min-width, md) {
					min-width: 120px;
				}
			}

			&__module ~ select.sui-select + .select2 {
				min-width: 200px;
				max-width: 200px;
				@include media(max-width, md) {
					display: block;
					min-width: 100%;
					max-width: 100%;
					margin-left: 0;
					margin-top: 10px;
					/* min-width: unset;
					flex: 1; */
				}
			}

			+ .sui-button {

				@include media(min-width, md) {
					margin-left: 10px;
				}
			}

			> form {
				display: flex;
				@include media(max-width, md) {
					display: block;
					.sui-button {
						margin-top: 10px;
					}
				}
			}

			@include media(max-width, md) {
				min-width: 100%;
				display: block;
				// flex: 0 0 100%;
				margin: 0 0 #{math.div($sui-gutter-md, 2)};
			}

			@include media(min-width, md) {
				align-items: center;
			}
		}

		.sui-button {

			+ .sui-button {

				@include media(max-width, md) {
					margin-left: #{math.div($sui-gutter-md, 2)};
				}

				@include media(min-width, md) {
					margin-left: auto;
				}
			}
		}

		@include media(max-width, md) {
			flex-wrap: wrap;
			justify-content: space-between;
			padding: #{math.div($sui-gutter, 2)} #{math.div($sui-gutter-md, 2)};
		}

		@include media(min-width, md) {
			padding: #{math.div($sui-gutter, 2)} $sui-gutter;
		}
	}

	.fui-pagination-entries {
		margin: $sui-gutter-md 0 #{math.div($sui-gutter-md, 2)};

		.sui-pagination-results {
			display: inline-block;
			line-height: 30px;

			+ .sui-pagination {
				margin-left: #{math.div($sui-gutter, 2)};
			}
		}

		.sui-pagination {
			flex: 0 0 auto;

			li a {

				@include media(max-width, md) {
					min-width: 38px;
				}
			}
		}

		@media ( min-width: 1113px ) {
			display: none;
		}
	}

	.fui-box-entries {

		.fui-box-actions {

			@include media(max-width, md) {
				padding: #{math.div($sui-gutter, 2)} #{math.div($sui-gutter-md, 2)};
			}
		}

		.sui-box-search {

			.sui-select-sm {
				min-width: 200px;

				+ .sui-button {

					@include media(max-width, md) {
						min-width: unset;
					}
				}
			}

			.sui-search-right {

				@include media(max-width, md) {
					display: none;
				}

				.sui-pagination-dots {
					min-width: 40px;
				}
			}
		}

		.fui-table-entries {

			> thead,
			> tbody {

				> tr > td {

					.fui-mobile-only {

						@include media(min-width, md) {
							display: none;
						}
					}
				}

				> tr:not(.sui-accordion-item-content) {

					> th, > td {

						&:first-child {

							.sui-checkbox {
								margin-right: 10px;
							}

							@include media(max-width, md) {
								min-width: 170px;
								display: flex;
								flex: 0 0 170px;
								align-items: center;
								padding-right: 0;
								padding-left: #{math.div($sui-gutter-md, 2)};
							}
						}

						&:not(:first-child) {

							.sui-accordion-open-indicator {

								@include media(max-width, md) {
									float: none;
									margin-left: 5px;
								}
							}

							@include media(max-width, md) {
								display: flex;
								flex: 1;
								align-items: center;
								justify-content: space-between;
								padding-right: #{math.div($sui-gutter-md, 2)};
							}
						}

						&:nth-child(n+3) {

							@include media(max-width, md) {
								display: none;
							}
						}

						@include media(max-width, md) {
							height: auto;
							padding-top: #{$sui-gutter-md - 1px};
							padding-bottom: #{$sui-gutter-md - 2px};
						}

						.forminator-submissions-column-content {
							display: flex;
							align-items: center;
						}

						.forminator-submissions-column-ellipsis {
							overflow: hidden;
							text-overflow: ellipsis;
						}

						.sui-tag.draft-tag {
							background: #888;
							color: #fff;
						}

						.sui-tag.status-tag {
							margin-left: 27%;

							@media (max-width: 1229px) {
								margin-left: 15px;
							}
						}
					}

					> th {
						text-overflow: ellipsis;
						overflow: hidden;
					}

					> td:last-child {

						.sui-accordion-open-indicator {
							margin-left: auto;
						}
					}

					@include media(max-width, md) {
						display: flex;
					}
				}

				> tr.sui-accordion-item-content {

					> td {

                        ul.bulleted {
                            padding-left: 15px;
                            list-style: disc;
                        }

						@include media(max-width, md) {
							height: auto;
							display: block;
							padding-right: #{math.div($sui-gutter-md, 2)};
							padding-left: #{math.div($sui-gutter-md, 2)};
						}
					}

					&.sui-accordion-item--open {

						@include media(max-width, md) {
							display: block !important;
							height: unset;
						}
					}
				}

				@include media(max-width, md) {
					display: block;
				}
			}

			@include media(max-width, md) {
				width: 100%;
				display: block;
			}
		}

		.fui-entry-content {

			.fui-entry-title {
				margin: 0 0 $sui-gutter-md;

				&:last-child {
					margin-bottom: 0;

					@include media(min-width, md) {
						margin-bottom: 0;
					}
				}

				@include media(min-width, md) {
					margin-bottom: $sui-gutter;
				}
			}

			.fui-table-details {
				margin: 0;

				> thead {

					tr th {
						padding-top: #{math.div($sui-gutter, 2)};
						padding-bottom: #{math.div($sui-gutter, 2)};

						@include media(max-width, md) {
							padding-top: 8px;
							padding-bottom: 9px;
						}
					}
				}

				thead,
				tbody {

					th, td {
						word-break: break-word;

						&:not(:last-child) {

							@include media(max-width, md) {
								display: none;
							}
						}
					}
				}
			}
		}
	}
}