@use "sass:math";

@include body-class(true) {

	// ELEMENT: Box Title
	.sui-box {

		.sui-box-title {

			[class*="forminator-icon-"] {
				float: left;
				display: block;
				margin-right: 10px;

				&:before {
					color: inherit;
					font-size: 20px;
					vertical-align: text-bottom;
				}
			}
		}
	}
}