@use "sass:math";

@include body-class(true) {

	// TYPE: Knowledge Quiz.
	.fui-entries--knowledge {

		.fui-entries-title,
		.fui-entries-subtitle {
			margin: 0 0 5px;
			color: palette(gray, dark);
			line-height: 30px;
			font-family: $font;
			font-weight: bold;
			letter-spacing: $font--letter-spacing;

			&:last-child {
				margin-bottom: 0;
			}

			+ .sui-notice {
				margin-top: 10px;
			}

			+ .fui-entries-table {
				margin-top: $sui-gutter-md;

				@include media(min-width, md) {
					margin-top: $sui-gutter;
				}
			}
		}

		.fui-entries-title {
			font-size: 22px;
		}

		.fui-entries-subtitle {
			font-size: 18px;
		}

		.sui-description {

			&:last-child {
				margin-bottom: 0;
			}

			+ .fui-entries-table {
				margin-top: $sui-gutter-md;

				@include media(min-width, md) {
					margin-top: $sui-gutter;
				}
			}
		}

		.fui-entries-block {
			padding: $sui-gutter-md 0;
			border-bottom: 1px solid palette(silver, soft);

			&:first-child {
				padding-top: 0;

				@include media(min-width, md) {
					padding-top: 0;
				}
			}

			&:last-child {
				padding-bottom: 0;
				border-bottom: 0;

				@include media(min-width, md) {
					padding-bottom: 0;
				}
			}

			@include media(min-width, md) {
				padding: $sui-gutter 0;
			}
		}

		.fui-entries-table {
			width: 100% !important;
			border-spacing: 0 !important;
			border-width: 0 !important;

			> thead > tr,
			> tbody > tr {

				> th, > td {
					height: #{$sui-gutter-md * 2};
					padding: #{math.div($sui-gutter-md, 2)} !important;
					border-bottom: 1px solid palette(silver, soft) !important;
					font-size: 13px;
					line-height: 22px;
					letter-spacing: $font--letter-spacing;

					&:first-child {
						border-left: 1px solid palette(silver, soft) !important;
						padding-left: #{$sui-gutter-md - 1px} !important;

						@include media(min-width, md) {
							padding-left: #{$sui-gutter - 1px} !important;
						}
					}

					&:last-child {
						border-right: 1px solid palette(silver, soft) !important;
						padding-right: #{$sui-gutter-md - 1px} !important;

						@include media(min-width, md) {
							padding-right: #{$sui-gutter - 1px} !important;
						}
					}

					@include media(min-width, md) {
						height: #{$sui-gutter * 2};
						padding: 10px #{math.div($sui-gutter, 2)} !important;
					}
				}
			}

			> thead > tr {

				> th {
					border-top: 1px solid palette(silver, soft) !important;
					color: palette(gray, dark);
					font-weight: bold;

					&:first-child {
						border-top-left-radius: $border-radius;
					}

					&:last-child {
						border-top-right-radius: $border-radius;
					}
				}
			}

			> tbody > tr {

				> td {
					color: palette(gray, light);
					font-weight: 500;

					strong {
						color: palette(gray, dark);
					}
				}

				&:last-child {

					> td {

						&:first-child {
							border-bottom-left-radius: $border-radius !important;
						}

						&:last-child {
							border-bottom-right-radius: $border-radius !important;
						}
					}
				}
			}

			> tfoot > tr {

				> td {
					padding: $sui-gutter-md 0 0 !important;

					.fui-entries-table-legend {
						display: block;

						p {
							display: block;
							position: relative;
							margin: 0 0 #{math.div($sui-gutter-md, 2)};
							padding: 5px 16px 5px 30px;
							border: 0;
							border-radius: $border-radius;
							background-color: palette(silver, light);
							color: palette(gray, default);
							font: 500 12px/16px $font;
							letter-spacing: $font--letter-spacing;

							&:before {
								content: " ";
								width: 8px;
								height: 8px;
								position: absolute;
								top: 9px;
								left: 16px;
								border-radius: 10px;
							}

							&.correct:before {
								background-color: $green;
							}

							&.incorrect:before {
								background-color: $red;
							}

							@include media(min-width, md) {
								min-width: 1px;
								flex: 0 1 auto;
								margin: 0 5px;
							}
						}

						@include media(min-width, md) {
							display: flex;
							flex-flow: row wrap;
							align-items: center;
							justify-content: center;
						}
					}

					@include media(min-width, md) {
						padding-top: $sui-gutter !important;
					}
				}
			}

			&[data-size="sm"] {

				> thead > tr,
				> tbody > tr {

					> th, > td {
						height: auto;

						&:first-child {

							@include media(min-width, md) {
								padding-left: #{$sui-gutter-md - 1px} !important;
							}
						}

						&:last-child {

							@include media(min-width, md) {
								padding-right: #{$sui-gutter-md - 1px} !important;
							}
						}

						@include media(min-width, md) {
							height: auto;
							padding: #{math.div($sui-gutter-md, 2)} !important;
						}
					}
				}
			}

			&[data-design="ghost"] {
				width: 100%;
				table-layout: fixed;
				border-spacing: 0 !important;
				border-collapse: collapse !important;

				> tbody > tr {
					height: auto;

					> td {
						vertical-align: top;
						border-bottom-width: 0 !important;
						padding: 5px #{math.div($sui-gutter-md, 2)} !important;

						&:first-child {
							width: 40%;
							border-left-width: 0 !important;
							padding-left: 0 !important;

							@include media(min-width, md) {
								width: 160px;
								padding-left: 0 !important;
							}
						}

						&:last-child {
							border-right-width: 0 !important;
							padding-right: 0 !important;
							font-weight: normal;

							strong {
								color: palette(gray, light);
								font-weight: 500;
							}

							@include media(min-width, md) {
								padding-right: 0 !important;
							}
						}

						@include media(min-width, md) {
							height: auto;
							padding: 5px #{math.div($sui-gutter-md, 2)} !important;
						}
					}

					&:first-child {

						> td {
							padding-top: 0 !important;
						}
					}

					&:last-child {

						> td {
							padding-bottom: 0 !important;
						}
					}
				}
			}
		}

		.sui-notice {

			p {

				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
}