@include body-class(true) {
	// Builder page.
	.forminator-pdf-form-builder {

		.sui-box {

			.fui-builder-section--insert {
				border-bottom-width: 1px;
			}

			.sui-box-status {
				.sui-status {
					.sui-status-changes {
						padding-left: 0;
						border-left: none;
					}
				}
			}
		}
	}

	// PDF fields selector.
	.pdf-form-field-selectors {
		.pdf-field-coming-soon {
			&:hover {
				cursor: help;
			}

			&.sui-tooltip {
				&::before {
					bottom: 95%;
				}
				&::after {
					bottom: 94%;
				}
			}
		}
	}

	// PDF Appearance settings.
	.pdf-appearance {

		.pdf-page-margin {

			select.sui-select {

				&+.sui-select {

					.selection {

						span[role=combobox].select2-selection--single {
							max-width: 40px;
							border-bottom-width: 2px;
							margin-left: auto;

							.select2-selection__rendered {
								font-size: 12px;
							}

							.select2-selection__arrow {
								width: 12px;
								height: 12px;
							}
						}
					}
				}
			}
		}
	}
}