@include body-class(true) {

	#forminator-poll-builder {

		.switch-box {
			text-align: right;
			margin-bottom: -27px;
		}
	}

	.fui-multi-answers {
		background-color: palette(silver, light);

		.fui-answers {
			margin: 0;
			padding: 0;

			&, > li {
				border: 0;
				list-style: none;
			}

			> li {
				display: flex;
				align-items: flex-start;
				margin: 0 0 10px;
				padding: 15px 20px;
				background-color: $white;
				border-radius: $border-radius;
				box-shadow: 0 2px 7px 0 rgba(0,0,0,0.05);
				transition: 0.2s ease;

				> span {
					display: block;

					&:not(:first-child) {
						margin-left: 10px;
					}
				}

				> .fui-answer--move {
					width: 30px;
					height: 30px;
					cursor: move;
					display: flex;
					align-items: center;
					justify-content: center;
					flex: 0 0 30px;
					margin: 5px 0;
					margin-left: -10px;
					text-align: center;
					color: palette(silver, medium);

					[class*="sui-icon-"] {
						display: block;
						font-size: 16px;

						&:before {
							display: block;
							color: inherit;
						}
					}
				}

				> .fui-answer--fields {
					flex: 1;
					flex-wrap: wrap;

					.sui-form-field {
						display: flex;
						flex: 0 0 100%;
						margin: 0;

						.answer-title {
							display: flex;
							width: auto;
							flex: 1 0 auto;

							input {
								flex: 1;
							}
						}

						.answer-image {
							display: flex;
							max-width: 250px;
							flex: 0 0 250px;
							padding-left: 10px;

							.sui-form-field {
								max-width: 100%;
							}

							.sui-upload {
								width: 100%;

								&.sui-has_file {

									.sui-upload-file {
										display: flex;
										flex: 1 1 auto;
										justify-content: center;
									}
								}

								&:not( .sui-has_file ) {

									.sui-upload-button {
										display: flex;
										flex: 1 1 auto;
										justify-content: center;
									}
								}

								.sui-upload-file {
									max-width: 100%;

									span {
										max-width: 113px;
										white-space: nowrap;
										overflow: hidden;
										text-overflow: ellipsis;
									}
								}
							}
						}

						@include media(max-width, md) {
							.answer-image {
								flex: 1 1 auto;
								padding-left: 0;
								padding-top: 10px;
								max-width: 100%;
							}
						}

						&:not(:last-child) {
							margin-bottom: 10px;
						}
					}

					@include media(max-width, md) {
						.sui-form-field {
							flex-direction: column;
						}
					}
				}

				> .fui-answer--actions {
					display: flex;
					flex: 0 0 70px;

					.sui-button,
					.sui-dropdown {
						display: block;
						margin: 0;
					}
				}

				&:hover {
					box-shadow: 0 0 0 4px rgba(0,0,0,0.02), 0 4px 15px 0 rgba(0,0,0,0.05);

					> .fui-answer--move {
						color: palette(gray, light);
					}
				}

				&.fui-error {
					box-shadow: inset 0 -2px 0 0 palette(red, default), 0 2px 7px 0 rgba(0,0,0,0.05);

					&:hover {
						box-shadow: inset 0 -2px 0 0 palette(red, default), 0 0 0 4px rgba(0,0,0,0.02), 0 4px 15px 0 rgba(0,0,0,0.05);
					}
				}

				&:last-child {

					@include media(max-width, md) {
						margin-bottom: $sui-gutter-md;
					}
	
					@include media(min-width, md) {
						margin-bottom: $sui-gutter;
					}
				}
			}
		}

		.fui-empty-message {
			text-align: center;

			.sui-image {

				@include media(max-width, md) {
					margin-top: $sui-gutter-md;
				}

				@include media(min-width, md) {
					margin-top: $sui-gutter;
				}
			}

			&:not(:first-child) {

				@include media(max-width, md) {
					margin-top: $sui-gutter-md;
				}

				@include media(min-width, md) {
					margin-top: $sui-gutter;
				}
			}
		}

		&:last-child {

			@include media(max-width, md) {
				margin-bottom: -#{$sui-gutter-md};
			}

			@include media(min-width, md) {
				margin-bottom: -#{$sui-gutter};
			}
		}

		@include media(max-width, md) {
			margin: $sui-gutter-md -#{$sui-gutter-md} 0;
			padding: $sui-gutter-md;
		}

		@include media(min-width, md) {
			margin: $sui-gutter -#{$sui-gutter} 0;
			padding: $sui-gutter;
		}
	}
}