@include body-class(true) {

	.sui-toggle {

		&.fui-highlighted-toggle {
			display: flex;
			flex-direction: row;
			align-items: flex-start;
			justify-content: center;
			padding: 19px 10px;
			background-color: palette(silver, light);

			.sui-toggle-slider {
				position: relative;
				flex: 0 0 auto;

				+ .sui-toggle-label {
					margin-left: 10px;
				}
			}

			.sui-toggle-label {
				min-width: 1px;
				flex: 0 1 auto;
				font-size: 13px;
			}
		}

		input[disabled] ~ .sui-toggle-slider {
			cursor: not-allowed;
			opacity: 0.5;
			background-color: #ddd;
		}
	}

	div.sui-form-field label[for="forminator-field-akismet-protection-behavior"] {
		margin-bottom: 10px;
	}

  	// TODO: Move this code to SUI module.
  	&.sui-color-accessible .sui-toggle input[type=checkbox]:not(:checked)+.sui-toggle-slider {
		background-color: unset;
		border: 1px solid #000;
		&:before {
			background-color: #000;
			width: 12px;
			height: 12px;
		}
	}
}