@include body-class(true) {

	.sui-insert-variables {
        &.textarea-type {

            select.sui-variables + .sui-select {

                .selection {

                    > span {
                        border-left: none;
                    }
                }
            }
        }

        select.sui-variables {

            + .sui-select {

                .selection {

                    span[role=combobox] {

                        .select2-selection__rendered {
                            padding-left: 0;
                            padding-right: 0;
                        }
                    }
                }
            }
        }
    }    
}