@include body-class(true) {
	.sui-recipient {

        &.sui-recipient-rounded {

            .sui-recipient-name {
                position: relative;
                padding-left: 30px;

                &:before {
                    font-size: 13px;
                    line-height: 20px;
                    width: 20px;
                    text-align: center;
                    border-radius: 100%;
                    background: #C4C4C4;
                }

                .subscriber,
                &::before {
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                }

                .subscriber {
                    height: 20px;
                    img {
                        width: 20px;
                        height: 20px;
                        border-radius: 100%;
                    }
                }
            }
        }
    }
}
