@include body-class(true) {

	.sui-tag-draft {
		background-color: $tag-draft-background;
		color: $tag-draft-color;
	}

	.sui-tag-grey {
		background-color: #888;
		color: $tag-published-color;
	}

	.sui-tag-published {
		background-color: $tag-published-background;
		color: $tag-published-color;
	}

	.pdf-tag.sui-tag.sui-tag-ghost {
		background-color: transparent;
		border-color: #286EFA;
		color: #286EFA;
		border-width: 1px;
		font-weight: 500;
		font-size: 9px;
		line-height: 16px;
		margin: 5px 10px;
		padding: 0 6px;
	}
}