@use "sass:math";

@include body-class(true) {

	.sui-box-body {

		// ELEMENT: Row (Slim)
		.sui-box-settings-slim-row {

			&.sui-sm {
				margin-bottom: #{math.div($sui-gutter-md, 2)};

				[class*="sui-box-settings-col-"] {

					+ div {

						@include media(max-width, md) {
							padding-top: 0;
						}

						@include media(min-width, md) {
							margin-left: $sui-gutter-md;
						}
					}

					@include media(max-width, md) { 

						&:first-child,
						&:last-child{
							display: inline-block;
						}

						&:first-child .sui-settings-label {
							width: 120px;
						}

					}
				}

				.sui-settings-label {

					&:only-child {

						@include media(min-width, md) {
							margin: 0;
							line-height: inherit;
						}
					}
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		// ELEMENT: Label
		.sui-settings-label {

			&.sui-sm {
				font-size: 13px;
				line-height: 22px;
			}
		}
	}
}