@include body-class(true) {

	.forminator-side-navigation-footer {

		.forminator-feedback {
			margin: 20px;
			cursor: pointer;
			box-sizing: border-box;
			display: flex;
			flex-direction: row;
			align-items: flex-start;
			padding: 15px;
			gap: 5px;
			background: #f8f8f8;
			border: 1px solid #e6e6e6;
			border-radius: 10px;

			.forminator-feedback-content {

				h4 {
					font-size: 15px;
					line-height: 24px;
					margin: 0;
					font-weight: 500;
					color: #000;
				}

				p {
					margin: 0;
					font-size: 13px;
					line-height: 20px;
					color: #333;
				}
			}
		}
	}
}

@media (max-width: 1249px) {

	@include body-class(true) {

		.forminator-side-navigation-footer {

			.forminator-feedback {
				margin: 10px 9px;
				padding: 5px;
				height: 42px;
				width: auto;

				.forminator-feedback-content {
					display: none;
				}
			}
		}
	}
}
