@include body-class(true) {

	.fui-select-inline {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px !important;

		label {
			margin: 0;
		}

		select {
			max-width: 100%;
			
			&.sui-select {

				+ .sui-select {
					margin-right: 0;
					margin-left: auto;

					.selection {

						span[role="combobox"] {

							&.select2-selection--single {
								border-width: 0;
								border-style: dashed;
								border-bottom-width: 1px;
								border-radius: 0;
								background-color: transparent;

								.select2-selection__rendered {
									font-size: 13px;
									padding-top: 0;
									padding-left: 0;
									padding-bottom: 1px;
									color: $fiftyshades;
									line-height: 20px;
								}

								.select2-selection__arrow {
									width: 20px;
									height: 20px;
								}
							}
						}
					}
				}

			}
		}
	}

	select.fui-select {
		width: 100%;
		height: 40px;
		display: block;
		flex: 1;
		margin: 0;
		padding: 4px 15px;
		border: 1px solid palette(gray, lighter);
		border-radius: $border-radius;
		background-color: #FAFAFA;
		box-shadow: none;
		color: palette(gray, dark);
		font-size: 15px;
		font-weight: 500;
		font-family: $font;
		letter-spacing: 0;
	}

	.select-container {

		// ELEMENT: Listing Actions.
		&.fui-select-listing-actions {
			width: 100%;
			max-width: 190px;
			min-width: unset;
			display: flex;

			.select-list-container {

				.list-results {
					max-height: 230px;
				}
			}
		}

		// ELEMENT: Empty Notice.
		&.fui-select-disabled {
			user-select: none;
			pointer-events: none;
			border-color: #DEDEDE;
			background-color: #F2F2F2;

			.select-list-container {
				cursor: not-allowed;

				.list-value {
					color: palette(silver, medium);
				}
			}

			&.active {
				user-select: none;
				pointer-events: none;

				.dropdown-handle {

					[class*=sui-icon-] {
						transform: rotate(0);
					}
				}

				.select-list-container {

					.list-results {
						width: 0;
						display: none;
						left: -9999px;
						right: -9999px;
						transition-duration: .3s;
					}
				}
			}
		}
	}

	.fui-select-with-delete {
		display: flex;
		flex-direction: row;
		align-items: center;

		.sui-form-field {
			min-width: 1px;
			flex: 1 1 auto;
			margin-bottom: 0;

			+ .fui-option-remove {
				margin-left: 10px;
			}
		}

		.fui-option-remove {
			flex: 0 0 auto;
			margin: 0;
		}
	}

	.sui-select.sui-select-dropdown-container--open {

		.sui-select-dropdown {

			.select2-results {

				.select2-results__options {

					&.select2-results__options--nested {
						max-height: 100%;
					}

					.select2-results__option {

						&.select2-results__option--highlighted {

							.sui-variable-value {
								color: #333;
							}
						}
					}
				}
			}

			&.sui-select-dropdown-loading {

				.select2-results {

					.select2-results__options {
	
						.select2-results__option {
	
							position: relative;
							margin: 0 15px;
							padding: 15px 20px;
							padding-left: 45px;
							border: 0;
							border-radius: 4px;
							background-color: #f2f2f2;
							color: #888;
							font: 400 13px / 22px Roboto, Arial, sans-serif;
							letter-spacing: -.25px;

							&::before {
								font-family: wpmudev-plugin-icons !important;
								content: "N";
								width: 30px;
								position: absolute;
								left: 10px;
								color: #888;
								font-size: 16px;
								text-align: center;
								animation: spin 1.3s linear infinite;
							}
						}
					}
				}
			}
		}
	}
}