@include body-class() {

	#forminator-permissions {
		.forminator-permissions-table {
			@include media(max-width, md) {
				overflow-x: scroll;
			}

			.sui-table {
				min-width: 740px;

				thead {
					th {
						height: auto;
					}
				}
			}

			& > .sui-accordion-header,
			.sui-accordion-item-header {
				min-height: 0;
			}

			.fp-role-head {
				.fp-item-name {
					font-weight: 500;
					color: #333;
				}
			}

			.fp-item-col {
				text-align: center;

				.sui-icon-check {

					&::before {
						color: #1ABC9C;
					}
				}

				.sui-icon-lock {

					&::before {
						color: #ddd;
					}
				}
			}

			.fp-role-box {
				display: flex;
				flex-direction: row;
				align-items: center;
				word-break: break-all;
			}

			.fp-role-img {
				display: flex;
				flex: 0 0 30px;
				width: 30px;
				height: 30px;
				text-align: center;
				border-radius: 50%;
				overflow: hidden;
				background-color: #777; /* temporary */
				background-color: #F8F8F8;
				justify-content: center;
				align-items: center;

				> span{
					line-height: 1;
					margin-right: 0 !important;
				}
			}

			.fp-role-info {
				display: flex;
				flex-direction: column;
				padding: 0 0 0 10px;
				min-width: 0;

				.sui-tooltip {
					pointer-events: initial;

					&::after {
						white-space: pre-line !important;
					}
				}
			}

			.fp-item-subtitle,
			.fp-item-name {
				overflow: hidden;
				display: inline-block;
				text-overflow: ellipsis;
				white-space: nowrap;
			}

			.fp-item-subtitle {
				font-size: 12px;
				font-weight: normal;
			}

			.permissions-checkboxes {
				margin-top: 15px;

				.sui-checkbox {
					margin-bottom: 0 !important;
				}
			}

			.permission-description {
				padding-left: 25px;
				margin-bottom: 10px !important;
			}
		}
    }

	#forminator-modal {

		.permissions-checkboxes {
			padding-top: 15px;

			> .sui-checkbox {
				margin-bottom: 5px;
			}

			> span {
				padding-left: 26px;
				padding-bottom: 10px;
				margin: 0;
			}
		}

		&.permissionDelete {

			.sui-description {

				b {
					color: #666;
				}
			}
		}
	}
}