@use "sass:math";

@include body-class() {

    #forminator-reports {

        .sui-sidenav {
            .sui-vertical-tabs {
                .sui-vertical-tab {
                    .sui-tag {
                        padding-bottom: 1px;
                    }
                }
            }
        }

        .sui-box__onload {
            .sui-box__message {
                opacity: 1;
                visibility: visible;
                z-index: 1;
                pointer-events: all;
            }
        }

        .sui-box__message {
            display: flex;
			align-items: center;
			justify-content: center;
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			padding: 5px $sui-gutter-md;
			background-color: rgba(255, 255, 255, 0.7);
            text-align: center;
            opacity: 0;
            visibility: hidden;
            transition: 0.25s ease;
            pointer-events: none;

            p {
                color: palette(gray, light);
				font: 500 12px/22px $font;
				letter-spacing: $font--letter-spacing;

                [class*=sui-icon-] {
                    vertical-align: text-bottom;
                    margin-right: 8px;
                    font-size: 16px;

                    &:before {
						display: block;
						color: palette(gray, light);
					}
                }
            }
        }

        .sui-box-header {
            a {
                [class*="sui-icon-"] {
                    &:before {
                        color: inherit;
                    }
                }
            }
        }

        .sui-box-footer {
            padding: 15px 30px;

            strong {
                color: #999;
            }

            p {
                font-weight: $font--weight-bold;
            }
        }

        button {
            &.view-reports {
                @include media(max-width, md) {
                    width: 100%;
                    margin-right: 0;
                }
            }
        }

        .fui-entries-bar {
            .sui-actions-right {

                .sui-form-field {
                    display: flex;

                    label {
                        margin-bottom: 0;
                        margin-right: 10px;
                    }

                    .sui-control-with-icon {

                        input {
                            font-size: 12px;
                            height: 30px;
                            padding: 7px 12px 7px 29px;
                            min-width: 220px;

                            @include media(max-width, md) {
                                min-width: 100%;
                                min-height: 100%;
                            }
                        }

                        [class*=sui-icon-] {
                            width: 12px;
                            height: 12px;
                            top: 8px;
                            left: 12px;
                        }

                        @include media(max-width, md) {
                            flex: 1;
                        }
                    }

                    @include media(max-width, md) {
                        margin-top: 10px;
                    }
                }

                @include media(max-width, md) {
                    display: block;
                    width: 100%;
                }
            }
        }

        .sui-flushed {
            margin-top: -20px;

            .sui-table {
                td {
                    padding-top: 20px;
                    padding-bottom: 20px;
                }

                .fui-table-title {
                    font-weight: $font--weight-bold;
                    font-size: 12px;
                    line-height: 22px;
                    color: #999;
                    letter-spacing: normal;
                    text-transform: uppercase;
                }

                .fui-table-content {
                    display: flex;
                    align-items: center;
                    font-weight: $font--medium;
                    font-size: 20px;
                    line-height: 30px;
                    letter-spacing: -0.25px;
                    color: $nightrider;
                    margin-top: 5px;
                }
            }
        }

        .sui-table-item-title {
            .fui-app--wrapper {
                color: #999;
            }
        }

        .forminator-reports-box {
            .sui-box {
                .sui-box-title {
                    [class*=sui-icon-] {
                        color: $grey;
                    }
                }
            }
        }

        // notifications
        .forminator-notifications {
            .notification-action {
                margin-top: 30px;
            }

            .forminator-default {
                box-shadow: inset 3px 0 0 palette(gray, light);
            }

            .sui-checkbox {
                margin-right: 10px;
            }

            .sui-notice {
                margin-top: 30px;
            }

            .sui-table {
                th {
                    .sui-checkbox {
                        input {
                            + span {
                                + span {
                                    font-size: 13px;
                                    font-weight: bold;
                                    color: $gray-alt;
                                }
                            }
                        }
                    }
                }

                th,
                td {
                    @include media(max-width, md) {
                        display: none;
                        &:first-child,
                        &:last-child {
                            display: table-cell;
                        }
                    }
                }
            }

            .subscriber-wrapper {
                display: flex;
                align-items: center;
                line-height: 1;
                font-weight: 500;
                color: palette(gray, light);
            }

            .subscriber {
                margin-right: 5px;
                img {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                }
            }

            .report-name-wrapper {
                display: flex;
                align-items: center;

                > div {
                    width: 100%;
                }

                .report-name {
                    color: $gray-alt;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                    padding-right: math.div($sui-gutter, 2);
                }

                .sui-tag.sui-tag-sm {
                    font-size: 9px;
                    line-height: 8px;
                    min-height: 12px;
                    padding: 0px 4px;
                }
            }

            .schedule {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .schedule-date {
                    flex: 1;
                    font-weight: 500;
                    font-size: 13px;
                    line-height: 22px;
                    letter-spacing: -0.25px;
                    color: palette(gray, light);
                }

                .sui-toggle {
                    width: 35px;
                    height: 22px;
                    margin-right: 10px;
                }
            }
        }
    }

    #wpbody-content .sui-wrap a.forminator-abandoned-submissions {
        color: #FF7E41;
    }

    .forminator-icons-inherit .sui-icon-unlock::before {
        color: inherit;
    }

    .daterangepicker.show-ranges {
        .ranges {
            margin-bottom: 20px;
        }
    }

    // reports popup
    #report-tab-settings-content {
        .sui-accordion {
            margin-top: 30px;
            margin-bottom: -30px;
            @include media(max-width, md) {
                margin-top: 20px;
                margin-bottom: -20px;
            }
        }
    }

    .report-button-with-toggle {
        display: flex;
        align-items: center;
        .sui-toggle {
            margin-right: 30px;

            .sui-toggle-slider {
                left: auto;
                right: 0;
                & ~ .sui-toggle-label {
                    margin-left: 0;
                    margin-right: 44px;
                }
            }
        }
    }

	// For Geolocation widget.
	.forminator-geo-state-block {
		max-height: 190px;
		overflow: auto;
	}
	#forminator_report_geolocation_widget {
		tbody {
			display: block;
			max-height: 300px;
			overflow-y: auto;
		}
		thead, table tbody tr {
			display: table;
			width: 100%;
			table-layout: fixed;
		}
	}
}