@include body-class(true) {

	.sui-notice {

		&#fui-notice-pro-fields {

			&.sui-notice-top {
				width: auto !important;
				max-width: unset !important;
				position: relative !important;
				z-index: 1 !important;
				top: auto !important;
				left: auto !important;
				margin-top: #{$sui-gutter-md - 5px};
				margin-left: -5px !important;
				margin-right: -5px !important;
				transform: none !important;
				box-shadow: none !important;

				@include media(min-width, md) {
					margin-top: #{$sui-gutter - 5px};
				}
			}
		}
	}

	.wpmudev-recommended-plugins {

		@include media(max-width, md) {
			display: none !important;
		}
	}
}

@include body-class(false) {
	&.sui-elements-accessible {
		.notice,
		.sui-notice {
			border-left-color: $accessible-dark;
			p {
				color: $accessible-dark !important;
			}
	
			a {
				color: $accessible-dark-alt;
			}
	
			a.button,
			[class^="button-"],
			button {
				background-color: $accessible-dark;
				border-color: $accessible-dark;
				color: $accessible-light;
			}
	
			.notice-dismiss{
				background: transparent;
				&::before {
					color: $accessible-dark;
				}
			}
		}
	}
}